<template>
	<div>
		<mt-header title="委托中的策略 ">
		  <router-link to="/user" slot="left">
		    <mt-button icon="back"></mt-button>
		  </router-link>
		</mt-header>
		<div class="positionsmain">
		    		<ul>
		    			<li>股票/数量</li>
		    			<li>止盈价/止损价 </li>
		    			<li>买入价/当前价</li>
		    			<li>买入时间 </li>
		    		</ul>
		    		<div class="positionssge" v-for="item in celue">
		    			<dl>
		    				<dt><span class="black">{{item.gupiao_name}}</span></dt>
		    				<dd><span>{{item.trush_number}}</span></dd>
		    			</dl>
		    			<dl>
		    				<dt><span class="red">{{item.stop_win}}</span></dt>
		    				<dd><span class="green">{{item.stop_down}}</span></dd>
		    			</dl>
		    			<dl>
		    				<dt>{{item.trush_price}}</dt>
		    				<dd><span class="red">{{item.now_price}}</span></dd>
		    			</dl>
		    			<dl>
		    				<dt>{{item.create_times}}</dt>
		    				<dd><span class="red">{{item.creat_time}}</span></dd>
		    			</dl>
		    		</div>
		    	</div>
		
	</div>
</template>

<script>
	import axios from 'axios';
	export default {
  data() {
    return {
    	celue:[]
    };
  },methods:{
  	getList(){
  		var api=this.$root.aurl+'api/user/apply_list';
   		 axios.post(api).then((res)=>{
   		 	console.log(res)
   		 	this.celue = res.data.data;
   		 })
  	},
  	
  },mounted(){
   		this.getList();
   		 
   }
}
</script>

<style>
	.positionsmain ul{width: 100%;height: 3rem;line-height: 3rem;overflow: hidden;border-bottom: 0.05rem solid #d3d3d3;}
	.positionsmain li{width: 23%;float: left;font-size: 1rem;color: #838383;}
	.positionsmain li:last-child{width: 31%;}
	.positionssge{width: 100%;overflow: hidden;border-bottom: 0.05rem solid #d3d3d3;padding-bottom: 0.5rem;}
	.positionssge dl{float: left;width: 23%;font-size: 1px;color: #656565;font-size: 1.4rem;}
	.positionssge dl:last-child{width: 31%;}
	.positionssge dl span{color: #b1b0b0;}
	.positionssge dt{margin-top: 0.9rem;margin-bottom: 0.45rem;}
	.positionssge .black{color: #000000;}
	.positionssge .red{color: #ec4229;}
	.positionssge .green{color: #4c9604;}
</style>


