<template>
	<div>
		<mt-header title="提现 ">
		  <router-link to="/user" slot="left">
		    <mt-button icon="back"></mt-button>
		  </router-link>
		</mt-header>
		<div class="wtd">
			<p>提现金额</p>
			<div class="clear"></div>
			<div class="wtdge">
				<p>￥</p>
				<input type="" placeholder="输入金额" value="" v-model="input1"/>
				<img @click="qingkong" src="../assets/cha.png" />
			</div>
			<div class="wtdge1">
				<p>可用余额<span ref="abc">216.03</span>元</p>
				<a @click="all">全部提现</a>
			</div>
			<div class="wtdge2">
				<p>支付密码：</p>
				<input type="password" v-model="input2"/>
			</div>
			<button class="wtdbtn" @click="wtdbtn">确认提现</button>
		</div>
	</div>
</template>

<script>
	import axios from 'axios'
	export default {
		data(){
			return{
				input1:'',
				input2:'',
			}
		},methods:{
			wtdbtn:function(){
				var that = this
				var input1 = this.input1;
				var input2 = this.input2;
				if(input1 == ''){
					that.$toast.center('请输入提现金额');
				}else{
					if(input2 == ''){
						that.$toast.center('请输入支付密码');
					}else{
					var qs = require('qs');
					var api=this.$root.aurl+'api/user/submitwithdraw';
     				axios.post(api,qs.stringify({'withdraw_money':input1,'vip_paypassword':input2})) .then(function(res){
    					console.log(res)
            				that.$toast.center(res.data.error);   
    					});
					}
					
				}
			},
			qingkong:function(){
				this.input1 = '';
			},
			all:function(){
				var text = this.$refs.abc.innerHTML;
				this.input1 = text;
			}
		}
	}
</script>

<style>
	.wtd > p {float: left;font-size: 1.3rem;color: #1a1e22;margin-top: 1.3rem;margin-left: 2.35rem;}
	.wtdge{width: 88.75%;height: 2.5rem;border-bottom: 0.05rem solid #9a9a9a;margin: 2.1rem auto 0;}
	.wtdge1{width: 88.75%;height: 2.5rem;margin: 0.5rem auto 0;}
	.wtdge p{float: left;line-height: 2.5rem;}
	.wtdge input{height: 2rem;margin-top: 0.25rem;border: 0;outline: none;float: left;width: 70%;margin-left: 1rem;}
	.wtdge img{width: 1.15rem;height: 1.15rem;margin-top: 0.675rem;margin-left: 1rem;}
	.wtdge1 p{float: left;}
	.wtdge1 a{float: right;color: #36acff;margin-right: 1rem;}
	.wtdbtn{width: 76.66%;height: 4rem;line-height: 4rem;background: #ec3c08;margin: 2.9rem auto 0;border: 0;outline: none;font-size: 1.5rem;color: #FFFFFF;border-radius: 0.15rem;}
	.wtdge2{width: 88.75%;height: 2.5rem;margin: 0.5rem auto 0;}
	.wtdge2 p{float: left;width: 20%;line-height: 2.4rem;}
	.wtdge2 input{float: left;width: 78%;border: 1px solid #DEDEDE;height: 2.4rem;padding-left: 1%;outline: none;}
</style>


