<template>
  <div class="footer">
    <div class="tab-item" @click="switchTo('/index')">
    	<img :src="'/index' === $route.path ? tabBarImgArr[0].selected : tabBarImgArr[0].normal"/>
      <span :class="{on: '/index' === $route.path}">首页</span>
    </div>
    
    <div class="tab-item" @click="switchTo('/optional')">
     <img :src="'/optional' === $route.path ? tabBarImgArr[3].selected : tabBarImgArr[3].normal" />      
      <span :class="{on: '/optional' === $route.path}">行情</span>
    </div>
    
    <div class="tab-item" @click="switchTo('/buy/000001')">
      <img :src="'/buy/'+id === $route.path ? tabBarImgArr[2].selected : tabBarImgArr[2].normal"/>
      <span :class="{on: '/buy/'+id  === $route.path}">策略</span>
    </div>
    <div class="tab-item" @click="switchTo('/press')">
      <img :src="'/press' === $route.path ? tabBarImgArr[1].selected : tabBarImgArr[1].normal" />      
      <span :class="{on: '/press' === $route.path}">新闻</span>
    </div>
    <div class="tab-item" @click="switchTo('/user')">
      <img :src="'/user' === $route.path ? tabBarImgArr[4].selected : tabBarImgArr[4].normal"/>    
      <span :class="{on: '/user' === $route.path}">我的</span>
    </div>
    
  </div>
</template>

<script>
	  export default {
    data () {
      return {
       tabBarImgArr:[
        {normal: require('../assets/footer01.png'), selected: require('../assets/footer02.png')},
        {normal: require('../assets/footer03.png'), selected: require('../assets/footer04.png')},
        {normal: require('../assets/footer05.png'), selected: require('../assets/footer06.png')},
        {normal: require('../assets/footer09.png'), selected: require('../assets/footer10.png')},
        {normal: require('../assets/footer07.png'), selected: require('../assets/footer08.png')},
      ],
      id:'',
      }
    },
methods:{
    switchTo(path){
      this.$router.push(path)
    }
},mounted(){
  	var id=this.$route.params.id; 
  	let token =sessionStorage.getItem('token');
  	//console.log(token)
  	if(id == 'undefined'){
				this.id = '000001';
			}else{
				this.id = id
			}
 }
  };
</script>

<style>
  .footer{
  	width: 100%;
  	height: 4rem;
  	overflow: hidden;
  	position: fixed;
  	left: 0;
  	bottom: 0;
  	background: #ffffff;
  }
  .tab-item{
  	width: 20%;
  	float: left;
  	text-align: center;
  }
  .tab-item img{
  	width: 1.8rem;
  	height: 1.8rem;
  	display: block;
  	margin: 0.55rem auto 0;
  }
  .tab-item span{
  	font-size: 1.2rem;
  	color: #666666;
  }
   .tab-item .on{
  	color: #ec4027;
  }
  .tab-item .on{
  	color: #ec4027;
  }
    
</style>


