<template>
	<div>
		<div class="loginbg"></div>
		<div class="loginnav">
		<ul>
			<li class="select">登录</li>
			<li>注册</li>
		</ul>
		<p></p>
		</div>
		<div class="fom" style="display: block;">
			<h3>登录</h3>
			<div class="clear"></div>
			<div class="fomge">
				<input type="text" placeholder="用户名/手机号" v-model="login1"/>
				<img src="../assets/phone.png" />
			</div>
			<div class="fomge">
				<input type="password" placeholder="在这里输入您的密码" v-model="login2"/>
				<img src="../assets/lock.png" />
			</div>
			<p @click="switchTo('/index')">返回首页</p>			
			<div class="pswbtn"><label><input type="checkbox" v-model="checked" checked><i>✓</i>记住账户</label></div>
			<div class="clear"></div>
			<button @click="loginbtn()">登录</button>
		</div>
		<div class="fom">
			<h3>注册</h3>
			<div class="clear"></div>
			<div class="fomge">
				<input type="text" placeholder="在这里输入您的手机号" v-model="reg1"/>
				<img src="../assets/phone.png" />
			</div>
			<div class="fomge">
				<input type="text" placeholder="在这里输入您的密码" v-model="reg2"/>
				<img src="../assets/lock.png" />
			</div>
			<div class="fomge">
				<input type="text" placeholder="请再次确认一遍密码" v-model="reg3"/>
				<img src="../assets/mark1.png" />
			</div>
			<div class="fomge">
				<input type="text" placeholder="邀请码" v-model="reg5"/>
				<img src="../assets/lock.png" />
			</div>
			<div class="fomge1">
				<input type="text" placeholder="输入验证码" v-model="reg4"/>
				<button @click="dx_yzm_bt"  ref="codetime">获取验证码</button>
			</div>
			<router-link :to="{ name:'zcxy', params: {id: 1} }">注册协议</router-link>
			<div class="clear"></div>
			<button @click="rechargebtn()">注册</button>
		</div>
	</div>
</template>

<script>
	import $ from 'jquery'
	import axios from 'axios'
	export default {
		components: {},
    	data: function () {
      		return {
      			login1:'',
      			login2:'',
      			reg1:'',
      			reg2:'',
      			reg3:'',
      			reg4:'',
      			reg5:'',
      			checked:true,
      		}
    	},mounted(){
    		this.getCookie();
    		var id=this.$route.query.code;
    		if(id != undefined){
    			$(".loginnav li").eq(1).addClass('select').siblings().removeClass('select');
    			$(".fom").hide().eq(1).show();
    			$(".loginnav p").addClass('animation')
    			this.reg5 = id
    		}
    		$(".loginnav li").on('click',function(){
    			$(".loginnav li").eq($(this).index()).addClass('select').siblings().removeClass('select');
    			$(".fom").hide().eq($(this).index()).show();
    		})
    		$(".loginnav li").eq(0).on('click',function(){
    			$(".loginnav p").removeClass('animation')
    		})
    		$(".loginnav li").eq(1).on('click',function(){
    			$(".loginnav p").addClass('animation')
    		})
    	},methods:{
    		switchTo(path){
      			this.$router.push(path)
   			},loginbtn(){
   				var qs = require('qs');
   				var login1 = this.login1;
   				var login2 = this.login2;
   				if(this.checked == true){
   					this.setCookie(login1,30);
   				}else{
   					this.clearCookie();
   				}
   				
   				var that = this;
   				
   				axios.post(this.$root.aurl+"api/base/login",qs.stringify({'username':login1,'password':login2})) 
    		.then(function(res){
    			//console.log(res)
    			if (res.data.code === 200) {
             					let data = res.data;
             					
             					that.$store.commit('SET_TOKEN',res.data.data.authKey);
            					
            						that.$router.push('/user')
            					
            				
							    
							}else{
								var tishi = res.data.error
    							that.$toast.center(tishi);
							}
    			
    		});
   			},setCookie(c_name, c_pwd, exdays){
   				var exdate = new Date();
   				exdate.setTime(exdate.getTime() + 24 * 60 * 60 * 1000 * exdays);
   				window.document.cookie = "userName" + "=" + c_name + ";path=/;expires=" + exdate.toGMTString();
                
   			},getCookie: function() {
                if (document.cookie.length > 0) {
                    var arr = document.cookie.split('; '); //这里显示的格式需要切割一下自己可输出看下
                    for (var i = 0; i < arr.length; i++) {
                        var arr2 = arr[i].split('='); //再次切割
                        //判断查找相对应的值
                        if (arr2[0] == 'userName') {
                            this.login1 = arr2[1]; //保存到保存数据的地方
                        } 
                    }
                }
            },clearCookie: function() {
                this.setCookie("", "", -1); //修改2值都为空，天数为负1天就好了
            },rechargebtn(){
   				var newname = this.reg1;
   				var reg2 = this.reg2;
   				var reg3 = this.reg3;
   				var reg4 = this.reg4;
   				var reg5 = this.reg5;
   				//console.log(reg2,reg3,reg4)
   				if(reg2 == reg3){
   				var qs = require('qs');
   				const url = this.$root.aurl+"api/base/register"
                	axios.post(url,qs.stringify({'vip_phone':newname,'vip_password':reg2,'vip_repassword':reg3,'phone_code':reg4,'recommendCode':reg5})).then((res)=> {
         				var _this = this;
    			if (res.data.status === 1) {
    				var tishi = res.data.message
    				this.$toast.center(tishi);
    				setTimeout( function(){
    					_this.$router.push('/login')
    				}, 2000);
    				
    			}else{
    				var tishi = res.data.message
    				this.$toast.center(tishi);
    			}
         			})
                	}else{
                		this.$toast.center('密码不一致，请确认');
                	}
   			},dx_yzm_bt:function(){
                	var phone = this.reg1;
                	var reg=/^1[3456789]\d{9}$/;
                	if(phone == ''){
                		this.$toast.center('请输入有效的手机号码');
                	}else if(!reg.test(phone)){
                		this.$toast.center('请输入有效的手机号码');
                	}else{
                	var newname = this.reg1;
                	var qs = require('qs');
                	const url = this.$root.aurl+"api/base/sendcode"
                	axios.post(url,qs.stringify({'vip_phone':newname})).then((response)=> {
         				if(response.data.status != 1){
         				var tishi = response.data.message
    					this.$toast.center(tishi);
         				}else{
         					this.$toast.center('验证码已发送');
         				}
         				
         				this.mekey = response.data.message;
         			}, function (error) {
         			console.log("请求失败",error);
        			})
      				var num = 60;
      				var codetime = this.$refs.codetime;
      				var timer = setInterval(function () {
      					num-- 
      					codetime.innerHTML = num + '秒后重新获取'
      					codetime.style.color = ' #fff'
      					codetime.disabled = ' disabled'
      					if (num === 0) { 
      						codetime.disabled = '' 
      						codetime.style.color = ' #fff'
      						codetime.innerHTML = '获取验证码'
      						clearInterval(timer) } 
      				}, 1000)
      				}
    	}
    	}
    
	}
</script>

<style>
	.loginbg{width: 100%;height: 15rem;background: url(../assets/loginbg.png) no-repeat;background-size: 100% 100%;}
	.loginnav{width: 69.44%;height: 3.5rem;background-color: #ffffff;box-shadow: 0px 20px 37px 13px rgba(3, 131, 144, 0.1);border-radius: 1.75rem;margin: -1.75rem auto 0;position: relative;}
	.loginnav p{width: 56%;height: 3.5rem;background-image: linear-gradient(90deg, #ff9936 0%, #ffc554 100%), linear-gradient(#000000, #000000);background-blend-mode: normal, normal;border-radius: 35px;position: absolute;left: 0;top: 0;z-index: 9;-webkit-transition:left .4s ease-out;}
	.loginnav ul{position: absolute;width: 100%;left: 0;top: 0;height: 3.5rem;z-index: 10;}
	.loginnav li{width: 50%;color: #666666;float: left;line-height: 3.5rem;font-size: 1.6rem;}
	.loginnav .select{color: #FFFFFF;}
	.loginnav .animation{left:44%}
	.fom{margin: 4.2rem auto 0; width: 90.27%;display: none;}
	.fom h3{float: left;font-size: 2.4rem;color: #3e4a59;margin-left: 2.5rem;}
	.fomge{width: 90.76%;margin: 0 auto;border-bottom: 0.05rem solid #d3dfef;height: 4.4rem;}
	.fomge input{width: calc(90% - 1rem);font-size:1.5rem;float: left;height: 4rem;border: 0;outline: none;margin-top: 0.2rem;padding-left: 1rem;}
	.fomge img{width: 1.1rem;height: 1.8rem;margin-top: 1.3rem;}
	.fom p{float: left;margin-left: 1rem;font-size: 1.3rem;color: #ff9936;margin-top: 1.8rem;}
	.fom a{float: right;margin-right: 0.6rem;font-size: 1.3rem;color: #ff9936;margin-top: 1.8rem;}
	.fom > button{
	width: 50.46%;
	height:  4rem;
	background-image: linear-gradient(90deg, #ff9936 0%, #ffc554 100%), linear-gradient(#ffffff, #ffffff);
	background-blend-mode: normal, normal;
	border: none;
	font-size: 1.8rem;
	color: #FFFFFF;
	margin-top: 1rem;
	outline: none;
	border-radius: 1rem;
	}
	.fomge1{width: 90.76%;margin: 0 auto;border-bottom: 0.05rem solid #d3dfef;height: 4.4rem;}
	.fomge1 input{width: calc(30% - 1rem);font-size:1.5rem;float: left;height: 4rem;border: 0;outline: none;margin-top: 0.2rem;padding-left: 1rem;}
	.fomge1 button{
		width: 60%;
	height: 3rem;
	margin-top: 0.7rem;
	margin-left: 10%;
	background-image: linear-gradient(90deg, #ff9936 0%, #ffc554 100%), linear-gradient(#ffffff, #ffffff);
	background-blend-mode: normal, normal;
	border-radius: 5rem;
	border: 0;
	outline: none;
	font-size: 1.6rem;
	color: #FFFFFF;
	}
	.pswbtn{float: right;margin-left: 1rem;font-size: 1.3rem;color: #ff9936;margin-top: 1.8rem;}
	.pswbtn label {font-size:12px;cursor:pointer;}
	.pswbtn label i {font-size:12px;font-style:normal;display:inline-block;width:12px;height:12px;text-align:center;line-height:12px;color:#fff;vertical-align:middle;margin:-2px 2px 1px 0px;border:#ff9936 1px solid;}
	.pswbtn input[type="checkbox"],input[type="radio"] {display:none;}
	.pswbtn input[type="radio"] + i {border-radius:7px;}
	.pswbtn input[type="checkbox"]:checked + i,input[type="radio"]:checked + i {background:#ff9936;}
	.pswbtn input[type="checkbox"]:disabled + i,input[type="radio"]:disabled + i {border-color:#ff9936;}
	.pswbtn input[type="checkbox"]:checked:disabled + i,input[type="radio"]:checked:disabled + i {background:#ff9936;}
</style>


