<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App'
}
</script>

<style>
	*{margin: 0;padding: 0;}
	html{font-size: 10px;}
	@media only screen and (min-width: 375px) {
	html {
		font-size: 10.5px !important;
	}
}
@media only screen and (min-width: 414px) {
	html {
		font-size: 11.5px !important;
	}
}
@media only screen and (min-width: 480px) {
	html {
		font-size: 12px !important;
	}
}
	li{list-style: none;}
#app {
  font-family: sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

.clear{clear: both;}
#app .mint-header{height: 4.1rem;background: #ec3c08;font-size: 1.4rem;}
#app .mint-switch-input:checked + .mint-switch-core{border-color: #ec4229;background-color: #ec4229;}
#app .mint-switch-core {
    width: 3.05rem;
    height: 1.8rem;
}
#app .mint-tab-item-label {
		font-size: 1.3rem;
		color: #646464;
	}
#app .mint-switch-core::before{width: 3.05rem;height: 1.8rem;}
#app .mint-switch-core::after{width: 1.4rem;height: 1.4rem;margin-top: 0.1rem;}
#app .mint-navbar .mint-tab-item.is-selected{
		color: #1d1d1d;
		border-bottom: 0.15rem solid rgba(236,63,37,0.99);
		margin-bottom: -0.07rem;
	}
#app .mint-tab-container{
		margin-bottom: 5rem;
	}
#app .mint-navbar{border-bottom: 0.05rem solid #c9c9c9;}
#app a{text-decoration: none;}
#app .mint-button::after{background: none;}
</style>



