<template>
	<div>
		<mt-header title="点买策略">
		  <router-link to="/" slot="left">
		    <mt-button icon="back"></mt-button>
		  </router-link>
		</mt-header>
		<div class="buy">
			<ul>
				<li @click="switchTo('/buy/000001')">创建策略</li>
				<li class="select">策略持仓</li>
			</ul>
		</div>
		<div class="positions">
		    	<div class="positionstop">
		    	<dl>
		    		<dt>账户资产</dt>
		    		<dd><span class="red">{{usermoney}}</span></dd>
		    	</dl>
		    	<dl>
		    		<dt>冻结金额</dt>
		    		<dd>{{vip_freeze}}</dd>
		    	</dl>
		    	<dl>
		    		<dt>可用资金</dt>
		    		<dd>{{vip_money}}</dd>
		    	</dl>
		    	<dl>
		    		<dt>持仓市值</dt>
		    		<dd>{{has_sum}}</dd>
		    	</dl>
		    	<dl>
		    		<dt>持仓盈亏</dt>
		    		<dd>{{yingkui}}</dd>
		    	</dl>
		    	<dl>
		    		<dt>昨日盈亏</dt>
		    		<dd><span class="green">{{yest_yingkui}}</span></dd>
		    	</dl>
		    	</div>
		    	<div class="clear"></div>
		    	<div class="strategyselect"><mt-navbar v-model="selected">
				  <mt-tab-item id="1">当前持仓</mt-tab-item>
				  <mt-tab-item id="2">当前委托</mt-tab-item>
				  <mt-tab-item id="3">历史交易</mt-tab-item>
				</mt-navbar>
				</div>
				<mt-tab-container v-model="selected">
  					<mt-tab-container-item id="1">
		    	<div class="positionsmain1">
		    		<ul>
		    			<li>股票/信用</li>
		    			<li>买价/当前价</li>
		    			<li>止盈/止损</li>
		    			<li>盈亏/持仓</li>
		    		</ul>
		    		<div class="positionsge" v-for="(item,index) in celue" >
		    			<span ref='suliang' style="display: none;">{{item.trush_number}}</span>
		    			<span ref='zsbili' style="display: none;">{{item.downstop}}</span>
		    			<span ref='id' style="display: none;">{{item.id}}</span>
		    			<div @click="show2(item.id)">
		    			<dl>
		    				<dt><span class="black">{{item.gupiao_name}}</span></dt>
		    				<dd><span ref='xinyongjin'>{{item.credit_money}}</span></dd>
		    			</dl>
		    			<dl>
		    				<dt ref='wt'>{{item.trush_price}}</dt>
		    				<dd ref="dangqj">{{item.now_price}}</dd>
		    			</dl>
		    			<dl>
		    				<dt><span class="red" ref="zhiying">{{item.stop_win}}</span></dt>
		    				<dd><span class="green" ref="zhisun">{{item.stop_down}}</span></dd>
		    			</dl>
		    			<dl>
		    				<dt><span :class="item.trush_price <= item.now_price ?'red':'green'">{{item.yingkui}}</span></dt>
		    				<dd><span>{{item.deal_number}}</span></dd>
		    			</dl>
		    			</div>
		    			<div class="clear"></div>
		    			<div class="psgemain">
			    			<p>上涨<span>{{item.stop_win_price}}元</span>将触发平仓</p>
			    			<button @click="zy(index)">修改止盈价</button>
			    			<button @click="zs(index)">修改止损价</button>
			    			<button class="buy_btn" @click="buy_btn(item.id)">卖出</button>
		    			</div>
		    			<div class="clear"></div>
		    			<div class="psgebottom">
		    				<p>递延费<span class="red">{{item.defer_money}}</span>元/万/天</p>
		    				<mt-switch @change="turn(index)" v-model="item.defer_status == 0?false:true" ></mt-switch>
		    			</div>
		    		</div>
		    		
		    	</div>
		    	 	</mt-tab-container-item>
					<mt-tab-container-item id="2">
						<div class="positionsmain">
		    		<ul>
		    			<li>股票/数量</li>
		    			<li>止盈价/止损价 </li>
		    			<li>委托价/当前价</li>
		    			<li>买入时间 /委托方式</li>
		    		</ul>
		    		<div class="positionssge" v-for="item in celue1">
		    			<dl>
		    				<dt><span class="black">{{item.gupiao_name}}</span></dt>
		    				<dd><span>{{item.trush_number}}</span></dd>
		    			</dl>
		    			<dl>
		    				<dt><span class="red">{{item.stop_win}}</span></dt>
		    				<dd><span class="green">{{item.stop_down}}</span></dd>
		    			</dl>
		    			<dl>
		    				<dt>{{item.trush_price}}</dt>
		    				<dd><span class="red">{{item.now_price}}</span></dd>
		    			</dl>
		    			<dl>
		    				<dt><span class="red">{{item.creat_time}}</span></dt>
		    				<dd>{{item.deal_type}}</dd>
		    			</dl>
		    		</div>
		    	</div>
					</mt-tab-container-item>
					<mt-tab-container-item id="3">
						<div class="positionsmain">
		    		<ul>
		    			<li>股票/数量</li>
		    			<li>收益/卖出类型 </li>
		    			<li>买入价/卖出价</li>
		    			<li>买入时间 /卖出时间 </li>
		    		</ul>
		    		<div class="positionssge" v-for="item in celue3">
		    			<dl>
		    				<dt><span class="black">{{item.gupiao_name}}</span></dt>
		    				<dd><span>{{item.deal_number}}</span></dd>
		    			</dl>
		    			<dl>
		    				<dt><span class="red">{{item.yingkui}}</span></dt>
		    				<dd><span class="green">{{item.sell_types}}</span></dd>
		    			</dl>
		    			<dl>
		    				<dt>{{item.now_price}}</dt>
		    				<dd><span class="red">{{item.sell_price}}</span></dd>
		    			</dl>
		    			<dl>
		    				<dt>{{item.create_times}}</dt>
		    				<dd><span class="red">{{item.sell_times}}</span></dd>
		    			</dl>
						<button @click="show1(item.id)">查看</button>
		    		</div>
		    	</div>
					</mt-tab-container-item>
				</mt-tab-container>
			</div>
		    <div class="tc tc1" v-show="inshow">
			<div class="tcmain">
				<div class="tc-title">
					<span>止盈价修改</span>
					<p @click="tcgb()">&#215;</p>
				</div>
				<div class="tc-main">
					<div class="tc-m">
					<p @click="tc1jian()">-</p>
					<input  v-model="tcmoney" ref='tcinput'/>
					<p @click="tc1jia()">+</p>
					</div>
					<button @click="tc1btn()">确认修改</button>
				</div>
			</div>
		</div>
		    <div class="tc tc2" v-show="inshow1">
			<div class="tcmain">
				<div class="tc-title">
					<span>止损价修改</span>
					<p @click="tcgb1()">&#215;</p>
				</div>
				<div class="tc-main1">
					<div class="tc-m">
					<p @click="tc2jian()">-</p>
					<input class="tcinput1"  v-model="tcmoney1" ref='tcinput1'/>
					<p @click="tc2jia()">+</p>
					
					</div>
					<input ref='tcinput2' class="tcinput2" placeholder="暂时不用追加保证金" disabled='disabled'/>
					<div class="clear"></div>
					<button @click="tc2btn()">确认修改</button>
				</div>
			</div>
		</div>
		<confirm ref="myConfirm"></confirm>
		<div class="popup" v-show="isShow">
			<div class="pmain">
				<div class="pmain-title">
					<span>策略详情</span>
					<p @click="hide()">&#215;</p>
				</div>
				<div class="pmain-m">
					<div class="pmain-m1">
						<p><span>交易明细</span></p>
						<ul>
							<li>
							<dl>
								<dt>{{gupiao_name}}</dt>
								<dd>{{gupiao_code}}</dd>
							</dl>
							</li>
							<li><dl>
								<dt>交易单号</dt>
								<dd>{{order_no}}</dd>
							</dl>
							</li>
							<li><dl>
								<dt>股票数量</dt>
								<dd>{{deal_number}}</dd>
							</dl>
							</li>
							<li>
								<dl>
								<dt>卖出类型</dt>
								<dd>{{sell_types}}</dd>
								</dl>
							</li>
							<li>
								<dl>
								<dt>买入价格</dt>
								<dd>{{defer_money}}</dd>
								</dl>
							</li>
							<li>
								<dl>
								<dt>卖出价格</dt>
								<dd>{{sell_price}}</dd>
								</dl>
							</li>
							<li>
								<dl>
								<dt>持仓时间</dt>
								<dd>{{chi_day}}天</dd>
								</dl>
							</li>
							
						</ul>
						<ol>
							<li>
								<dl>
								<dt>买入时间</dt>
								<dd>{{deal_times}}</dd>
								</dl>
							</li>
							<li>
								<dl>
								<dt>卖出时间</dt>
								<dd>{{sell_times}}</dd>
								</dl>
							</li>
						</ol>
					</div>
					<div class="pmain-m2">
						<p><span>资金明细</span></p>
						<ul>
							<li>
								<dl>
								<dt>交易本金</dt>
								<dd>{{all_money}}</dd>
								</dl>
							</li>
							<li>
								<dl>
								<dt>信用金</dt>
								<dd>{{credit_money}}</dd>
								</dl>
							</li>
							<li>
								<dl>
								<dt>交易综合费</dt>
								<dd>{{service_money}}</dd>
								</dl>
							</li>
							<li>
								<dl>
								<dt>续期费用</dt>
								<dd>{{defer_money}}</dd>
								</dl>
							</li>
							<li>
								<dl>
								<dt>返还信用金</dt>
								<dd>{{repay_creat_money}}</dd>
								</dl>
							</li>
							<li>
								<dl>
								<dt>盈亏分配</dt>
								<dd>{{repay_profits}}</dd>
								</dl>
							</li>
						</ul>
					</div>
				</div>
			</div>
		</div>
		<div class="popup" v-show="isShow1">
			<div class="pmain">
				<div class="pmain-title">
					<span>策略详情</span>
					<p @click="hide1()">&#215;</p>
				</div>
				<div class="pmain-m">
					<div class="pmain-m1">
						<p><span>交易明细</span></p>
						<ul>
							<li>
							<dl>
								<dt>{{gupiao_name}}</dt>
								<dd>{{gupiao_code}}</dd>
							</dl>
							</li>
							<li><dl>
								<dt>交易单号</dt>
								<dd>{{order_no}}</dd>
							</dl>
							</li>
							<li><dl>
								<dt>持仓数量</dt>
								<dd>{{deal_number}}</dd>
							</dl>
							</li>
							<li>
								<dl>
								<dt>信用金倍率</dt>
								<dd>{{credit_rate}}倍</dd>
								</dl>
							</li>
							<li>
								<dl>
								<dt>信用金</dt>
								<dd>{{credit_money}}</dd>
								</dl>
							</li>
							<li>
								<dl>
								<dt>服务费</dt>
								<dd>{{service_money}}</dd>
								</dl>
							</li>
							<li>
								<dl>
								<dt>递延费</dt>
								<dd>{{defer_money}}</dd>
								</dl>
							</li>
							<li>
								<dl>
								<dt>已付递延费</dt>
								<dd>{{pay_defer_money}}</dd>
								</dl>
							</li>
							
						</ul>
						<ol>
							<li>
								<dl>
								<dt>买入时间</dt>
								<dd>{{creat_time}}</dd>
								</dl>
							</li>
						</ol>
					</div>
					
				</div>
			</div>
		</div>

		

		<mi-footer></mi-footer>
	</div>
</template>

<script>
	import footer from './footer';
	import axios from 'axios'
	import confirm from './confirm'
	export default {
  		name: 'page-navbar',
	components: {
      'mi-footer': footer,
      'confirm': confirm,
      
    },
  data() {
    return {
     inshow:false,
     inshow1:false,
     selected: '1',
     tcmoney:'',
     tcmoney1:'',
     tcmoney2:'',
     celue:[],
     usermoney:'',
     vip_freeze:'',
     vip_money:'',
     has_sum:'',
     yingkui:'',
     yest_yingkui:'',
     index:'',
     celue1:[],
     celue3:[],
     isShow:false,
     isShow1:false,
     gupiao_name:'',
     gupiao_code:'',
     order_no:'',
     deal_number:'',
     sell_types:'',
     defer_money:'',
     deal_times:'',
     sell_price:'',
     sell_times:'',
     chi_day:'',
     all_money:'',
     credit_money:'',
     service_money:'',
     defer_money:'',
     repay_creat_money:'',
     repay_profits:'',
     pay_defer_money:'',
     credit_rate:'',
     creat_time:''
    }
    },methods:{
    	switchTo(path){
      			this.$router.push(path)
   		},popup(){
   			
   		},
    	getList(){
   		var api1=this.$root.aurl+'api/user/assets';
   		axios.get(api1).then((res)=>{
   			if(res.data.code == 999){
   				this.$router.push('/login');
   		 		this.$store.dispatch('token');
   		 	}else{
   		 	//	console.log(res)
   		 	this.usermoney = res.data.data.money_account;
   		 	this.vip_freeze=res.data.data.money.vip_freeze;
   		 	this.vip_money = res.data.data.money.vip_money;
   		 	this.has_sum=res.data.data.gethas.has_sum;
   		 	this.yingkui=res.data.data.gethas.yingkui;
   		 	this.yest_yingkui=res.data.data.gethas.yest_yingkui;
   		 	}
   		 })
   		 var api=this.$root.aurl+'api/user/trade_list';
   		 axios.get(api).then((res)=>{
   		// 	console.log(res)
   		 	this.celue = res.data.data;
   		 })
   	},zy(index){
          	this.inshow = !this.inshow
          	this.index = index;
          	 	this.tcmoney = this.$refs.zhiying[index].innerHTML;
          },zs(index){
          	this.inshow1 = !this.inshow1
          	this.index = index;
          	 	this.tcmoney1 = this.$refs.zhisun[index].innerHTML;
          },tc1jian(){
          	var that = this
          	var dangqj = this.$refs.dangqj[this.index].innerHTML;
          	var i = 0;
  			i--;
  			var n=this.$refs.tcinput.value;
  			var num = parseFloat(n)+i/100;
  			if(num >= dangqj){
  				this.$refs.tcinput.value =num.toFixed(2);
  			}else{
  				that.$toast.center('止盈价格不得低于当前价');
  				return false;
  			}
         },tc2jian(){
         	var wt = this.$refs.wt[this.index].innerHTML;
         	var shuliang = this.$refs.suliang[this.index].innerHTML;
         	var xinyongjin = this.$refs.xinyongjin[this.index].innerHTML;
         	var zsbili = this.$refs.zsbili[this.index].innerHTML;
         	var mindownstop_price = parseFloat(((wt*shuliang)-(xinyongjin*zsbili / 100))/shuliang).toFixed(2);
          	var that = this
          	var i = 0;
  			i--;
  			var n = this.$refs.tcinput1.value;
  			var num1 = parseFloat(n)+i/100;
  			//console.log(num1)
  			if(num1 < mindownstop_price){
  				this.$refs.tcinput1.value = num1.toFixed(2);
  				this.$refs.tcinput2.value = '0';
  				this.$refs.tcinput2.disabled='';
  			}else if(num1 < 0){
  				that.$toast.center('止损价不得小于等于0');
  			}else{
  				this.$refs.tcinput1.value = num1.toFixed(2);
  			}
         },tc1jia(){
         	var i = 0;
  			i++;
  			var n=this.$refs.tcinput.value;
  			var num = parseFloat(n)+i/100;
			this.$refs.tcinput.value =num.toFixed(2);
         },tc2jia(){
         	var i = 0;
  			i++;
  			var n=this.$refs.tcinput1.value;
  			var num = parseFloat(n)+i/100;
			this.$refs.tcinput1.value =num.toFixed(2);
         },tcgb(){
          	this.inshow = !this.inshow
          },tcgb1(){
          	this.inshow1 = !this.inshow1
          	this.$refs.tcinput2.disabled='disabled';
          	this.$refs.tcinput2.value = '';
          },tc1btn(){
          	var that = this;
          	var dangqj = this.$refs.dangqj[this.index].innerHTML;
          	var n= this.$refs.tcinput.value;
          	var id = this.$refs.id[this.index].innerHTML;
          	//console.log(id)
          	if(n > dangqj){
          		var api=this.$root.aurl+'api/user/setstopstatus';
          		axios.get(api,{params:{'type':1,'val':n,'id':id,'credit':0}}).then((res)=>{
                     that.$toast.center(res.data.message);
                     this.inshow = !this.inshow
                   //  this.getList();
                })
          	}else{
          		that.$toast.center('止盈价格不得低于当前价');
          	}
          },tc2btn(){
          	var that = this;
          	var n= this.$refs.tcinput1.value;
          	var id = this.$refs.id[this.index].innerHTML;
          	var wt = this.$refs.wt[this.index].innerHTML;
         	var shuliang = this.$refs.suliang[this.index].innerHTML;
         	var xinyongjin = this.$refs.xinyongjin[this.index].innerHTML;
         	var zsbili = this.$refs.zsbili[this.index].innerHTML;
         	var mindownstop_price = parseFloat(((wt*shuliang)-(xinyongjin*zsbili / 100))/shuliang).toFixed(2);
          	var yingdownstop_price = parseInt((wt*shuliang-n)/zsbili*100)
          	var shuru = this.$refs.tcinput2.value
          //	console.log(mindownstop_price)
          	if(n < mindownstop_price){
          		if(this.$refs.tcinput2.value < yingdownstop_price){
          		that.$toast.center('如需修改为该止损价格，您最少应该追加信用金'+yingdownstop_price+'元');
          		this.$refs.tcinput2.disabled='';
          		this.$refs.tcinput2.value = yingdownstop_price;
          		}else{
          			var api=this.$root.aurl+'api/user/setstopstatus';
          		axios.get(api,{params:{'type':2,'val':n,'id':id,'credit':shuru}}).then((res)=>{
                     that.$toast.center(res.data.message);
                     this.inshow1 = !this.inshow1
                     this.$refs.tcinput2.value = '';
                     this.$refs.tcinput2.disabled='disabled';
                     this.getList();
                })
          		}
          	}else{
          		var api=this.$root.aurl+'api/user/setstopstatus';
          		axios.get(api,{params:{'type':2,'val':n,'id':id,'credit':0}}).then((res)=>{
                     that.$toast.center(res.data.message);
                     this.inshow1 = !this.inshow1
                    this.$refs.tcinput2.value = '';
                    this.$refs.tcinput2.disabled='disabled';
                     this.getList();
                })
          	}
          },buy_btn(e){
          	var titleText = '卖出策略';
          	var content = '您确定要提交委托卖出该持仓策略吗？';
          	this.$refs.myConfirm.show(titleText,content,e)
          },turn(index){
          	var onoff = this.celue[index].defer_status;
          	var id = this.celue[index].id;
          	var qs = require('qs');
          	var that = this;
          	
          	if(onoff == 1){
          		var api = this.$root.aurl+'api/user/change_defer';
          		axios.post(api,qs.stringify({'id':id,'status':0})).then((res)=>{
          			that.$toast.center(res.data.data);
          		})
          		this.getList();
          	}else if(onoff == 0){
          		var api = this.$root.aurl+'api/user/change_defer';
          		axios.post(api,qs.stringify({'id':id,'status':1})).then((res)=>{
          			that.$toast.center(res.data.data);
          		})
          		this.getList();
          	}
          },getData(){
  		var api=this.$root.aurl+'api/user/apply_list';
   		 axios.post(api).then((res)=>{
   		 //	console.log(res)
   		 	this.celue1 = res.data.data;
   		 })
  	},getData1(){
  		var api=this.$root.aurl+'api/user/history_trade';
   		 axios.get(api).then((res)=>{
   		 	//console.log(res)
   		 	this.celue3 = res.data.data.list;
   		 })
  	},show1(v){
  		var api=this.$root.aurl+'api/user/oldinfo';
  		var qs = require('qs');
  		axios.post(api,qs.stringify({'id':v})).then((res)=>{
   		 	//console.log(res)
   		 	//console.log(res.data.code)
   		 	this.gupiao_name = res.data.data.gupiao_name;
   		 	this.gupiao_code = res.data.data.gupiao_code;
   		 	this.order_no = res.data.data.order_no;
