   		 	this.repay_profits = res.data.data.repay_profits;
   		 	this.pay_defer_money = res.data.data.pay_defer_money;
   		 	this.credit_rate = res.data.data.credit_rate;
   		 	this.creat_time = res.data.data.creat_time;
   		 })
  		this.isShow1 = !this.isShow1;
  	},hide(){
  		this.isShow = !this.isShow;
  	},hide1(){
  		this.isShow1 = !this.isShow1;
  	}
    },mounted(){
  	this.getList();
  	this.getData();
  	this.getData1();
    }
 }
</script>

