<template>
	<div>
		<div class="header">
			<router-link to="/" class="headerleft">
			    <mt-button icon="back" ></mt-button>
  			</router-link>
  			<ul>
  				<li  @click="switchTo('/quotation')">自选</li>
  				<li class="select" @click="switchTo('/optional')">行情</li>
  			</ul>
  			<router-link :to="{ name:'search1'}" class="headerright">
			    <mt-button icon="search" ></mt-button>
  			</router-link>
		</div>
		<div class="optional">
		<div class="optshow">
			<ul >
				<li>
					<p>{{sh.name}}</p>
					<dl :class="sh.price_range > 0?'red':'green'">
						<dt>{{sh.current_price}}</dt>
						<dd>{{sh.price_range}}&nbsp;&nbsp;&nbsp;&nbsp; {{sh.price_rate}}%</dd>
					</dl>
				</li>
				<li>
					<p>{{sz.name}}</p>
					<dl :class="sz.price_range > 0?'red':'green'">
						<dt>{{sz.current_price}}</dt>
						<dd>{{sz.price_range}}&nbsp;&nbsp;&nbsp;&nbsp; {{sz.price_rate}}%</dd>
					</dl>
				</li>
				<li>
					<p>{{cb.name}}</p>
					<dl :class="cb.price_range > 0?'red':'green'">
						<dt>{{cb.current_price}}</dt>
						<dd>{{cb.price_range}}&nbsp;&nbsp;&nbsp;&nbsp; {{cb.price_rate}}%</dd>
					</dl>
				</li>
				
			</ul>
		</div>
		<div class="popular">
			<h3><span>热门行业</span></h3>
			<ul>
				<li v-for="item in host">
					<p>{{item[1]}}</p>
					<span :class="item[5] > 0?'red':'green'">{{parseFloat(item[5]).toFixed(2)}}%</span>
					<dl>
						<dt>{{item[12]}}</dt>
						<dd>{{item[10]}}&nbsp;&nbsp;&nbsp;&nbsp;{{item[9]}}%</dd>
					</dl>
				</li>
				
			</ul>
		</div>
		<div class="rankings">
			<h3 @click="toup()"><span>涨幅榜</span></h3>
			<div class="rankings-warp" ref='rankingsup' :style="contentStyleObj">
			<div class="rankings-ge"  v-for="(item,index) in trade_up" >
				<router-link :to="{ name:'stock', params: {id: item.code} }">
				<dl>
					<dt>{{item.name}}</dt>
					<dd>{{item.code}}</dd>
				</dl>
				<div class="rankmoney">{{item.trade}}</div>
				<p class="red">{{item.changepercent}}%</p>
				</router-link>
			</div>
			</div>
		</div>
		<div class="rankings">
			<h3 @click="todown()"><span>跌幅榜</span></h3>
			<div class="rankings-warp" ref='rankingsdown' :style="contentStyleObj1">
			<div class="rankings-ge" v-for="(item,index) in trade_down">
				<router-link :to="{ name:'stock', params: {id: item.code} }">
				<dl>
					<dt>{{item.name}}</dt>
					<dd>{{item.code}}</dd>
				</dl>
				<div class="rankmoney">{{item.trade}}</div>
				<p class="green">{{item.changepercent}}%</p>
				</router-link>
			</div>
			</div>
		</div>
		</div>
		<mi-footer></mi-footer>
	</div>
</template>

<script>
	import footer from './footer';
	import axios from 'axios'
	export default {
	components: {
      'mi-footer': footer
    },
  data() {
    return {
      sh:[],
      sz:[],
      cb:[],
      host:[],
      trade_up:[],
      trade_down:[],
      contentStyleObj:{
      	height:''
      },
      contentStyleObj1:{
      	height:''
      }
    };
  },methods:{
    		switchTo(path){
      			this.$router.push(path)
   			},getData1(){
           	 var api = this.$root.aurl+'api/index/market_bat';
           	 axios.get(api,{params:{'code':'sh000001,399001,399006'}}).then((res)=>{
           	 	this.sh = res.data.data[0];
           	 	this.sz = res.data.data[1];
           	 	this.cb = res.data.data[2];
           	 })
          },getData2(){
          	var api = this.$root.aurl+'api/index/sinahy';
          	axios.get(api).then((res)=>{
          		//console.log(res);
          		this.host = res.data.data;
          	})
          },getData3(){
          	var api = this.$root.aurl+'api/index/stock_top10';
          	axios.get(api).then((res)=>{
          	//	console.log(res);
          		this.trade_up = res.data.data
          	})
          },getData4(){
          	var api = this.$root.aurl+'api/index/stock_bot10';
          	axios.get(api).then((res)=>{
          		this.trade_down = res.data.data;
          	})
          },toup(){
          	var heg = this.$refs.rankingsup.offsetHeight;
          	
          	if(heg != 0){
          		this.contentStyleObj.height='0px';
          	}else{
          		this.contentStyleObj.height='auto';
          	}
          	},todown(){
          	var heg = this.$refs.rankingsdown.offsetHeight;
          	if(heg != 0){
          		this.contentStyleObj1.height='0px';
          	}else{
          		this.contentStyleObj1.height='auto';
          	}
          }
    },mounted(){
  		this.getData1();
  		this.getData2();
  		this.getData3();
  		this.getData4();
  }
}
</script>

<style>
	.header{width: 100%;height: 4.1rem;background: #ec3c08;overflow: hidden;display: flex;}
	.header .mint-button{background: none;color: #FFFFFF;height:4.1rem;border: 0;box-shadow: none;}
	.header ul{display: flex;height: 3.1rem;margin-top: 0.5rem;border: 0.05rem solid #FFFFFF;border-radius: 0.5rem;overflow: hidden;-webkit-box-align: center;font-size: 18px;-webkit-box-pack: center;justify-content: center;white-space: nowrap;flex: 1 1 0%;}
	.header li{float: left;width: 50%;line-height: 2.75rem;font-size: 1.2rem;color:#ffffff;}
	.header .select{background: #FFFFFF;color: #ec3c08;}
	.headerleft{display: flex;flex: 1 1 0%;}
	.headerright{display: flex;flex: 1 1 0%;justify-content: flex-end;}
	.optshow {width: 100%;overflow: hidden;}
	.optshow li{width: calc(33% - 0.06rem);float: left;margin-top: 1.15rem;height: 7.35rem;border-right: 0.05rem solid #b2b0b0;}
	.optshow li:last-child{border-right: 0;}
	.optshow li p{font-size: 1.4rem;color: #272521;margin-top: 0.8rem;}
	.optshow li dl{font-size: 1.5rem;color: #ec3c08;line-height: 2.5rem;}
	.optshow li dd{font-size: 1.2rem;line-height: 2rem;}
	.popular{width: 100%;overflow: hidden;}
	.optional{width: 100%;overflow: hidden;margin-bottom: 6rem;}
	.popular h3{width: 100%;height: 2.25rem;line-height: 2.25rem;background: #f4f4f8;margin-top: 1.4rem;}
	.popular h3 span{font-size: 1.35rem;color: #606060;float: left;margin-left: 1.5rem;font-weight: 500;}
	.popular li{width: calc(33% - 0.06rem);border-right: 0.05rem solid #DDDDDD;border-top: 0.05rem solid #DDDDDD;float: left;padding-bottom: 0.85rem;}
	.popular li:nth-child(1){border-top: 0;}
	.popular li:nth-child(2){border-top: 0;}
	.popular li:nth-child(3){border-top: 0;}
	.popular li p{font-size: 1.4rem;color: #000000;margin-top: 0.65rem;}
	.popular li span{font-size: 1.4rem;color: #ec3c08;line-height: 1.5rem;}
	.popular li dl{font-size: 1.2rem;color: #5e5d5d;line-height: 1.5rem;}
	.popular li:nth-child(3n+3){border-right: 0;}
	.rankings h3{width: 100%;height: 2.5rem;line-height: 2.5rem;background: #f4f4f8;}
	.rankings h3 span{font-size: 1.35rem;float: left;margin-left: 1.5rem;font-weight: 500;}
	.rankings-warp{width: 100%;overflow: hidden;}
	.rankings-ge{width: 100%;overflow: hidden;margin-top: 0.6rem;margin-bottom: 0.6rem;height: 4rem;}
	.rankings-ge dl{float: left;font-size: 1.3rem;margin-left: 1.3rem;margin-top: 0.5rem;}
	.rankings-ge dt{color: #000000;}
	.rankings-ge dd{color: #727373;}
	.rankmoney{float: left;font-size: 1.3rem;color: #000000;line-height: 4rem;width: 52%;display: block;text-align: center;}
	.rankings-ge p{float: right;width: 7.35rem;height: 2.2rem;line-height: 2.2rem;margin-top:0.9rem;color: #FFFFFF;font-size: 1.3rem;border-radius: 0.5rem;margin-right: 5%;}
	.rankings-ge .red{background: #ec3c08;}
	.rankings-ge .green{background: #3da32c;}
	.popular li .green{color: #05AA3B;}
	.popular li .red{color: #FF4500;}
	.optshow li .green{color: #05AA3B;}
	.optshow li .red{color: #FF4500;}
	
</style>


