<template>
	<div>
		<mt-header :title="code">
			  <router-link to="/index" slot="left">
			    <mt-button icon="back"></mt-button>
			  </router-link>
		</mt-header>
		<div class="xyname" v-html="data">
			{{data}}
		</div>
	</div>
	
</template>

<script>
	import axios from 'axios'
	export default {
    data(){
    	return{
    		code:'',
    		data:''
    	}
    },
        mounted(){
                var id=this.$route.params.id;
                //调用请求数据的方法

                this.requestData(id);

        },
    methods:{
    	requestData(id){
                var api=this.$root.aurl+'api/index/getadvert?type='+id;
                axios.get(api).then((res)=>{
                     console.log(res)
                        this.code=res.data.data.name;
                        this.data=res.data.data.content;
                })
            },
    switchTo(path){
      
      this.$router.push(path)
    }
   }
    }
</script>

<style>
	.xyname p{text-align: left;text-indent: 2rem;font-size: 1rem;width: 90%;margin: 0 auto;}
</style>


