<template>
	<div>
		<mt-header title="点买策略">
		  <router-link to="/" slot="left">
		    <mt-button icon="back"></mt-button>
		  </router-link>
		</mt-header>
		<div class="buy">
			<ul>
				<li class="select">创建策略</li>
				<li @click="switchTo('/strategy')">策略持仓</li>
			</ul>
		</div>
		    <div class="strategy">
		    	<div class="strategytop">
		    		<div class="strleft">
		    			<h3>{{title}}({{code}})<img @click="switchTo('/search')" src="../assets/magnifier.png"/></h3>
		    			<dl>
		    				<dt>{{jk}}</dt>
		    				<dd>{{zf}}&nbsp;&nbsp;&nbsp;&nbsp;{{zd}}%</dd>
		    			</dl>
		    		</div>
		    		<div class="strright">
		    			<button></button>
		    			<div class="clear"></div>
		    			<dl>
		    				<dt>今开</dt>
		    				<dd>{{jk1}}</dd>
		    			</dl>
		    			<dl>
		    				<dt>最高</dt>
		    				<dd>{{zg}}</dd>
		    			</dl>
		    			<dl>
		    				<dt>昨收</dt>
		    				<dd>{{zs}}</dd>
		    			</dl>
		    			<dl>
		    				<dt>最低</dt>
		    				<dd>{{zd1}}</dd>
		    			</dl>
		    		</div>
		    		
		    	</div>
		    	<!--<div class="strategyshow">
		    		<ul>
		    			<li>今开    <span>{{jk1}}</span></li>
		    			<li>最高    <span>{{zg}}</span></li>
		    			<li>涨停价    <span style="color: #ec4228">{{ztj}}</span></li>
		    			<li>昨收    <span>{{zs}}</span></li>
		    			<li>最低    <span>{{zd1}}</span></li>
		    			<li>跌停价    <span style="color: #11B72F">{{dtj}}</span></li>
		    			<li>成交量    <span>{{cjl}}手</span></li>
		    			<li>成交额    <span>{{cje}}万</span></li>
		    		</ul>
		    	</div>-->
		    	<div class="strategytable">
		    		<div class="sty-ge">
		    			<p>信用金</p>
		    			<div class="stymoney">
		    				<input type="text" ref="stymoney" @blur="stysmoney()" v-model="stymoney"/>
		    				<span>元</span>
		    			</div>
		    		</div>
		    		<div class="sty-ge">
		    			<p>推荐</p>
		    			<div class="stymoneybtn">
		    				<ul>
		    					<li v-on:click="aa($event,index)" v-for="(item,index) in items" :class="{active:index == tabIndex1}">{{item}}</li>
		    				</ul>
		    			</div>
		    		</div>
		    		<div class="sty-ge">
		    			<p>倍率</p>
		    			<div class="stymultiplebtn">
		    				<dl v-for="(item,index) in bl" @click="stymultiplebtn(index)" :class="{active:index == tabIndex}">
		    					<dt v-on:click="beishu($event)">{{item.rate}}倍</dt>
		    					<!--<dd>本金：{{item.rect_money}}元</dd>-->
		    				</dl>
		    				
		    			</div>
		    		</div>
		    		<div class="sty-ge">
		    			<p>止盈价格</p>
		    			<div class="styprofit">
		    				<span class="zreduce" @click="zyjian()">-</span>
		    				<input type="number" :value="zyjg" class="red zinput" ref="zhang"  @blur.prevent="changeCount()"/>
		    				<span class="zadd" @click="zyjia()">+</span>
		    				
		    				<p>涨至<span class="red">{{zybl}}%</span>发起平仓</p>
		    			</div>
		    		</div>
		    		<div class="sty-ge">
		    			<p>止损价格</p>
		    			<div class="styprofit">
		    				<span class="dreduce" @click="zsjian()">-</span>
		    				<input type="number" :value="zsjg" class="green dinput" ref="dei" @blur.prevent="changeCount1()"/>
		    				<span class="dadd" @click="zsjia()">+</span>
		    				
		    				<p>跌至<span class="green">{{zsblzs}}%</span>发起平仓</p>
		    			</div>
		    		</div>
		    		<div class="sty-ge">
		    			<p>可买股数<img @click=""  src="../assets/mark.png"></p>
		    			<span><samp class="red">{{kgsl}}</samp>股</span>
		    			<small>市值{{parseInt(kgsl*jk)}}</small>
		    			<div class="clear"></div>
		    			</div>
		    			<div class="sty-ge">
		    			<p>自动续期<img @click="show2"  src="../assets/mark.png"></p>
		    			<mt-switch v-model="valuezd" class="zd"></mt-switch>
		    			<small>递延费{{strategy_renewal_fee}}元/万/天</small>
		    			<div class="clear"></div>
		    			</div>
		    			<div class="sty-ge">	
		    			<p>综合费用<img @click="show3"  src="../assets/mark.png"></p>
		    			<span><samp class="red">{{fwf}}</samp>元</span>
		    			<small>含前两个交易日服务费</small>
		    		</div>
		    		<div class="sty-bottom">
		    			<!--<ul>
		    				<li>总计扣款：<span>{{zjkk}}元</span></li>
		    				<li>可用余额：<span>{{yue}}元</span></li>
		    			</ul>
		    			<div class="clear"></div>
		    			<button class="cancel" @click="optional()" >{{sdata1}}</button>
		    			<button class="sure" @click="surebtn()">发起策略</button>-->
		    			<div class="clear"></div>
		    			<label><input type="checkbox" checked="checked"/>我已阅读并同意<router-link :to="{ name:'zcxy', params: {id: 2} }">《平台策略协议》</router-link></label>
		    		</div>
		    	</div>
		    </div>
			<div class="buybottom">
				<dl>
					<dt>需支付 <span>{{zjkk}}</span> 元</dt>
					<dd>(账户余额{{yue}}元)</dd>
				</dl>
				<button class="sure" @click="surebtn()">{{zjkk > yue?'余额不足':'发起策略'}}</button>
			</div>
	</div>
</template>

<script>
	import axios from 'axios'
	import $ from 'jquery'
	export default {
  		name: 'page-navbar',
	
  data() {
    return {
      selected: '1',
      valuezd:true,
      title:'',
      code:'',
      jk:'',//当前价
      zd:'',
      zf:'',
      zs:'',//昨收
      zg:'',//最高
      zd1:'',//最低
      jk1:'',//今开价
      ztj:'',//涨停价
      dtj:'',//跌停价
      cjl:'',//成交量
      cje:'',//成交额
      stymoney:'',//信用金
      sdata1:'',//自选状态
      bl:[],//倍率
      dqj:'',//当前价
      items:[],
      zjkk:'0.00',//总计扣款
      fwf:'',//服务费
      strategy_renewal_fee:'',
      active:false,
      beis:'',//第一个倍率
      yue:'',//可用余额
      tabIndex:'0',
      tabIndex1:-1, 
      zybl:'', //止盈比例
      zyjg:'', //止盈价格
      zsjg:'', //止损价格
      zxxyj:'', //最小信用金
      kgsl:'', //可购数量
      zsblzs:'', //止损比例
      zxzsj:'',//最小止损价
      csfwf:'',//初始服务费
    }
  },methods:{
  	switchTo(path){
      			this.$router.push(path)
   		},
   	getList1(){
   		var api1=this.$root.aurl+'api/user/getvipmoney';
   		axios.get(api1).then((res)=>{
   			// console.log(res)
   			if(res.data.code == '999'){
   				this.$router.push("/login");
   		 	//	this.$store.dispatch('token');
   			}else{
   				this.yue = res.data.data
   			}
   			
   		})
   	},show2(){
  		this.$toast.center('次日14:40之前满足续期条件将自动申请续期；若条件不符或余额不足，策略将被卖出');
  	},show3(){
  		this.$toast.center('交易综合费包括建仓费，证券公司交易费及印花税等');
  	},getData(id){
                    //请求数据
                    var api=this.$root.aurl+'api/index/invest?code='+id;
                    axios.get(api).then((res)=>{
   		 					//console.log(res)
   		 					if(res.data.code == '400'){
   		 					this.title = res.data.data.title;
   		 					this.code = res.data.data.code;
   		 					this.zd = res.data.data.zd;
   		 					this.zf = res.data.data.zf;
   		 					this.fwf = res.data.data.strategy_fee;
   		 					this.strategy_renewal_fee = res.data.data.strategy_renewal_fee;
   		 					this.items = res.data.data.recommond_money
   		 					this.getput();
   		 					}else{
   		 					//	console.log(res)
   		 					this.title = res.data.data.title;
   		 					this.code = res.data.data.code;
   		 					this.jk = res.data.data.hq_info[3];
   		 					this.jk1 = res.data.data.hq_info[5];
   		 					this.zs = res.data.data.hq_info[4];
   		 					this.zg = res.data.data.hq_info[6];
   		 					this.zd1 = res.data.data.hq_info[7];
   		 					this.ztj = (res.data.data.hq_info[4]*1.1).toFixed(2);
   		 					this.dtj = (res.data.data.hq_info[4]*0.9).toFixed(2);
   		 					this.dqj = res.data.data.hq_info[3];
   		 					this.cjl = res.data.data.hq_info[10];
   		 					this.cje = res.data.data.hq_info[12];
   		 					this.zd = res.data.data.zd;
   		 					this.zf = res.data.data.zf;
   		 					this.csfwf = res.data.data.strategy_fee;
   		 					this.strategy_renewal_fee = res.data.data.strategy_renewal_fee;
   		 					//this.money = res.data.data.money;
   		 					this.bl = res.data.data.rate_list;
   		 					this.zybl = res.data.data.up_rate;
   		 					this.zsbl = res.data.data.down_rate;
   		 					this.zyjg = res.data.data.up_price;
   		 					this.beis = res.data.data.rate_list[0].rate;
   		 					this.stymoney = Math.ceil(parseInt(this.jk*100/(parseInt(this.beis)))/100)*100;
   		 					this.zxxyj = Math.ceil(parseInt(this.jk*100/(parseInt(this.beis)))/100)*100;
   		 					this.fwf = (res.data.data.strategy_fee*(this.zxxyj*this.beis/10000)).toFixed(2);
   		 					this.kgsl = parseInt(((+parseInt(this.zxxyj)*parseInt(this.beis))/this.jk)/100)*100
   		 					this.zsjg =((parseFloat(this.jk)*parseInt(this.kgsl)-parseInt(this.zxxyj)*this.zsbl/100)/parseInt(this.kgsl)).toFixed(2);
   		 					this.zsblzs=((parseFloat(this.jk) - parseFloat(this.zsjg)).toFixed(2)/parseFloat(this.jk) *100).toFixed(2)
   		 					this.zjkk = parseFloat(this.fwf)+parseFloat(this.zxxyj)
   		 					this.items = res.data.data.recommond_money
   		 					this.getput();
   		 					}
   		 			})    
          },getData1(id){
          	var api=this.$root.aurl+'api/index/get_code_info?code='+id;
                    axios.get(api).then((res)=>{
                    		//console.log(res)
                    		this.title = res.data.data.title;
   		 					this.code = res.data.data.code;
   		 					this.jk = res.data.data.hq_info[3];
   		 					this.jk1 = res.data.data.hq_info[5];
   		 					this.zs = res.data.data.hq_info[4];
   		 					this.zg = res.data.data.hq_info[6];
   		 					this.zd1 = res.data.data.hq_info[7];
   		 					this.ztj = (res.data.data.hq_info[4]*1.1).toFixed(2);
   		 					this.dtj = (res.data.data.hq_info[4]*0.9).toFixed(2);
   		 					this.dqj = res.data.data.hq_info[3];
   		 					this.cjl = res.data.data.hq_info[10];
   		 					this.cje = res.data.data.hq_info[12];
   		 					this.zd = res.data.data.zd;
   		 					this.zf = res.data.data.zf;
   		 					this.getput();
                    })
          },stymultiplebtn(e){
          	this.tabIndex = e;
          },beishu(e){
          	this.beis = parseInt(e.currentTarget.innerText);
          	var stymoneys = this.$refs.stymoney.value;
          	this.kgsl = parseInt(parseFloat((parseInt(stymoneys*this.beis))/parseFloat(this.jk))/100)*100
          	let kgsl = parseInt(parseFloat((parseInt(stymoneys*this.beis))/parseFloat(this.jk))/100)*100
         	this.zsjg=(parseInt(this.jk*kgsl-stymoneys* this.zsbl/ 100) / parseInt(kgsl)).toFixed(2);
         	this.zsblzs = ((parseFloat(this.jk)-parseFloat(this.zsjg)).toFixed(2)/parseFloat(this.jk)*100).toFixed(2)
          	this.fwf = (this.csfwf*(stymoneys*this.beis/10000)).toFixed(2);
          	
          	this.zjkk = parseInt(stymoneys)+parseFloat(this.fwf);
          },stysmoney(){
          	
          	var ordmoney = this.$refs.stymoney.value;
          	
          	var nowmoney = this.zxxyj
          	
          	if(ordmoney < nowmoney){
          	//	console.log(111)
          		this.stymoney = nowmoney;
          		this.$toast.center('最少信用金为'+nowmoney);
          	}else{
          		this.stymoney = ordmoney;
          	}
          	
          	for(var i = 0;i<this.bl.length;i++){
          		this.bl[i].rect_money=parseInt(parseInt(parseInt((parseInt(this.stymoney)+parseInt(this.stymoney *this.bl[i].rate))/this.dqj)/100)*100*this.dqj);
          	}
          	this.fwf = (this.csfwf*(this.stymoney*this.beis/10000)).toFixed(2);
          	this.zjkk = parseInt(this.stymoney)+parseFloat(this.fwf);
          	
          },aa(e,index){
          	this.tabIndex1 = index;
          	var stymoneys = e.currentTarget.innerText;
          	this.stymoney = stymoneys;
          	this.kgsl = parseInt(parseFloat((parseInt(stymoneys*this.beis))/parseFloat(this.jk))/100)*100
          	let kgsl = parseInt(parseFloat((parseInt(stymoneys*this.beis))/parseFloat(this.jk))/100)*100
          	this.fwf = (this.csfwf*(stymoneys*this.beis/10000)).toFixed(2);
          	
          	for(var i = 0;i<this.bl.length;i++){
          		this.bl[i].rect_money=parseInt(parseInt(parseInt((parseInt(stymoneys)+parseInt(stymoneys *this.bl[i].rate))/this.dqj)/100)*100*this.dqj);
          	}
          	this.zsjg=(parseInt(this.jk*kgsl-stymoneys* this.zsbl/ 100) / parseInt(kgsl)).toFixed(2);
          	this.zsblzs = ((parseFloat(this.jk)-parseFloat(this.zsjg)).toFixed(2)/parseFloat(this.jk)*100).toFixed(2)
          	this.zjkk = parseInt(stymoneys)+parseFloat(this.fwf);
          },surebtn(){
          	var title = this.title;
          	var code = this.code; 
          	var stymoneys = this.stymoney;
          	if(this.valuezd == true){
          		var valuezd = 1;
          	}else{
          		var valuezd = 0;
          	}
          	var beishu =this.beis;
          	var zhang = this.$refs.zhang.value;
          	var dei = this.$refs.dei.value;
          	var shuliang = this.kgsl;
          	var price = this.dqj
          	var qs = require('qs');
          	let token =sessionStorage.getItem('token');
          	if(this.zjkk > this.yue){
          		this.$toast.center("余额不足");
          	}else{
          	axios.post(this.$root.aurl+'api/user/submit_invest',qs.stringify({ 'key':token,'stname':title,'stcode':code,'money':stymoneys,'strategy_rate':beishu,'autostatus':valuezd,'winstops':zhang,'downstops':dei,'number':shuliang,'price':price }))
           .then((res)=>{
           				
             			if (res.data.code === 200) {
             					this.$toast.center(res.data.data);
             					setTimeout(() => {
                    				this.$router.push('/mestrategy1')
                				}, 2000);
							}else{
								var tishi = res.data.error
    							this.$toast.center(tishi);
    							
							}
          				}).catch(function (error) {
          					console.log(error)
        					})
          				}
          }/*,optional(){
          	var qs = require('qs');
          	var title = this.title;
          	var that =this;
          	var code = this.code;
          	var api=this.$root.aurl+'api/user/add_gprss';
     				axios.post(api,qs.stringify({'gupiao_name':title,'gupiao_code':code})) .then(function(res){
    					//console.log(res)
            				that.$toast.center(res.data.error);   
    				});
    				var id=this.$route.params.id; 
    				this.getData(id);
          }*/,getput(){
          	var qs = require('qs');
          	var that =this;
          	var code = this.code;
          	var api=this.$root.aurl+'api/user/getrss_status';
             axios.post(api,qs.stringify({'gupiao_code':code})) .then(function(res){
    					if(res.data.code == '200'){
    						that.sdata1 = res.data.data; 
    					}else{
    						that.sdata1 = res.data.data;
    					}
    		});
          },zyjia(){
          	var i = 0;
  			i++
  			var n=this.$refs.zhang.value;
  			var num = parseFloat(n)+i/100;
			this.zyjg = num.toFixed(2);
          	this.zybl =((parseFloat(this.zyjg)-parseFloat(this.jk))*100/parseFloat(this.jk)).toFixed(2); 
          },zyjian(){
          	var i = 0;
  				i--;
  			var n=this.$refs.zhang.value;
  			var num = parseFloat(n)+i/100;
  			if(num >= this.jk){
  			this.zyjg = num.toFixed(2);
  			this.zybl =((parseFloat(this.zyjg)-parseFloat(this.jk))*100/parseFloat(this.jk)).toFixed(2); 
  			}else{
  			return false;
  			}
         },zsjia(){
         	var i = 0;
  			i++;
  			var n=this.$refs.dei.value;
  			var num = parseFloat(n)+i/100;
			this.zsjg = num.toFixed(2);
			this.zsblzs =((parseFloat(this.jk)-parseFloat(this.zsjg))*100/parseFloat(this.jk)).toFixed(2);
			//console.log(this.zsblzs)
         },zsjian(){
         	var i = 0;
  			i--;
  			var n=this.$refs.dei.value;;
  			var num = parseFloat(n)+i/100;
  			var stymoneys = this.$refs.stymoney.value
  			var zsjg = parseInt(this.jk*this.kgsl-stymoneys* this.zsbl/ 100) / parseInt(this.kgsl);
  			if(num < zsjg){
  			this.$toast.center('最小止损价格为'+ zsjg);
  			return false;
  			}else{
  			this.zsjg = num.toFixed(2);
  			this.zsblzs =((parseFloat(this.jk)-parseFloat(this.zsjg))*100/parseFloat(this.jk)).toFixed(2);
  			}
         },changeCount(){
         	var n=this.$refs.zhang.value;
         	this.zybl =((parseFloat(n)-parseFloat(this.jk))*100/parseFloat(this.jk)).toFixed(2); 
         }
  	
  },mounted(){
  	var id=this.$route.params.id; 
  	this.getData(id)
  	this.getList1();
  	if(id != 'undefined'){
				var myDate = new Date();
				var week = myDate.getDay();
				if(myDate.getMinutes() <= 9){
				var currentTime = myDate.getHours() + "0" + myDate.getMinutes();
				}else{
				var currentTime = myDate.getHours() + "" + myDate.getMinutes();
				}	
		if(week == 6 || week == 0){
				clearInterval(this.timer);        
    			this.timer = null;
    			this.getData1(id)
			}else{
				if (currentTime > 930 && currentTime < 1130) {
				  this.timer=setInterval(() => { 
            	this.getData1(id)
        		}, 3000)        
           }else if(currentTime > 1300 && currentTime < 1500){
           		this.timer=setInterval(() => { 
            		this.getData1(id)
        		}, 3000)
           }else{
           		clearInterval(this.timer);        
    			this.timer = null; 
    			this.getData1(id)
           }
			}
			}else{
				this.getData1('000001');
			}
  }, destroyed (){
  		clearInterval(this.timer);        
    	this.timer = null;
  }
}
</script>

<style>
	.buy{width: 100%;overflow: hidden;}
	.buy li{font-size: 1.3rem;color: #646464;border-bottom: 0.05rem solid #c9c9c9;width: 50%;float: left;height: 4rem;line-height: 4rem;}
	.buy .select{border-bottom: 0.05rem solid #c9c9c9;color: rgba(236,63,37,0.99);height: 4rem;line-height: 4rem;} 
	.mint-switch{float: right;margin-right: 3.61%;margin-top: 0.35rem;}
	.strategy{width: 100%;overflow: hidden;margin-bottom: 5.5rem;}
	.sty-ge .mint-switch-core{width: 3.05rem;height: 1.8rem;}
	.mint-switch-core::before{width: 3.05rem;height: 1.8rem;}
	.mint-switch-core::after{width: 1.4rem;height: 1.4rem;margin-top: 0.1rem;}
	.mint-tab-container{margin-bottom: 6rem;}
	.strategytop{width: 100%;background: linear-gradient(#fab39d,#f8a891,#f8744d);border-bottom: 0.1rem solid #dddcdc;overflow: hidden;}
	.strleft{width: 40%;overflow: hidden;float: left;}
	.strleft h3{font-size: 1.35rem;font-weight: 500;color: #FFFFFF;margin-left: 1rem;margin-top: 1rem;}
	.strleft h3 img{width: 1.2rem;margin-left: 0.5rem;}
