<template>
	<div>
		<mt-header title="修改登录密码 ">
		  <router-link to="/data" slot="left">
		    <mt-button icon="back"></mt-button>
		  </router-link>
		</mt-header>
		<div class="password">
			<ul>
				<li>
					<p>原密码</p>
					<input type="password" placeholder="请填写原密码" v-model="input1"/>
				</li>
				<li>
					<p>新密码</p>
					<input type="password" placeholder="请填写新密码" v-model="input2"/>
				</li>
				<li>
					<p>确认新密码</p>
					<input type="password" placeholder="请确认新密码" v-model="input3"/>
				</li>
			</ul>
			<button class="passwordbtn" @click="passwordbtn()">确认</button>
		</div>
		<mi-footer></mi-footer>
	</div>
</template>

<script>
	import footer from './footer';
	import axios from 'axios';
	export default {
  		name: 'page-navbar',
	components: {
      'mi-footer': footer
    },
  data() {
    return {
     input1:'',
     input2:'',
     input3:'',
    };
  },methods:{
  	passwordbtn(){
  		var input1 = this.input1;
  		var input2 = this.input2;
  		var input3 = this.input3;
  		var qs = require('qs');
    	let token =sessionStorage.getItem('token');
    	if(input2 == input3){
    		axios.post(this.$root.aurl+'api/user/edit_password',qs.stringify({'key':token,'password':input1,'newpass':input2}))
   		 .then((res)=>{
   		 	//console.log(res)
   		 	this.$toast.center(res.data.message);
   		 		
   		 }).catch(function (error) {
          		console.log(error)
        	})
    	}else{
    		this.$toast.center('确认密码不一致');
    	}
   		
   	}
  }
}
</script>

<style>
	.password{width: 100%;overflow: hidden;}
	.password li{width: 100%;height: 3.6rem;line-height: 3.6rem;border-bottom: 0.05rem solid #d6d6d6;}
	.password li p{float: left;margin-left: 1.85rem;width: 5.8rem;font-size: 1.1rem;color: #404040;text-align: left;}
	.password li input{width: 50%;height: 3rem;margin-top: 0.3rem;float: left;margin-left: 4.2rem;border: 0;outline: none;}
	.passwordbtn{width: 61.38%;height: 3.3;line-height: 3.3rem;border-radius: 0.15rem;background: #ec3c08;margin: 4.4rem auto 0;border: 0;color: #FFFFFF;font-size: 1.3rem;outline: none}
</style>


