<template>
	<div>
		<mt-header title="实名认证">
		  <router-link to="/data" slot="left">
		    <mt-button icon="back"></mt-button>
		  </router-link>
		</mt-header>
		<div class="real">
			<ul>
				<li>
					<p>姓名</p>
					<input type="text" placeholder="请填写于身份证一致的姓名" v-model="input1"/>
					
				</li>
				<li>
					<p>身份证号码</p>
					<input type="text" placeholder="请填写身份证号码" v-model="input2"/>
				</li>
			</ul>
			<div class="realshow">注：实名认证后则不可修改</div>
			<button class="realbtn" @click="realbtn()">确认</button>
			<mi-footer></mi-footer>
		</div>
	</div>
</template>

<script>
	import footer from './footer';
	import axios from 'axios';
	export default {
  		name: 'page-navbar',
	components: {
      'mi-footer': footer
    },
  data() {
    return {
    	input1:'',
    	input2:''
    };
  },methods:{
  	realbtn(){
  		var input1 = this.input1;
  		var input2 = this.input2;
  		var that = this
  		var qs = require('qs');
  		if(input1 == ''||input2 == ''){
  			this.$toast.center('信息输入不完整');
  		}else{
  			let token =sessionStorage.getItem('token');
   		axios.post(this.$root.aurl+'api/user/idcard_validate',qs.stringify({'key':token,'vip_realname':input1,'vip_idcard':input2}))
   		 .then((res)=>{
   		 	that.$toast.center(res.data.data);
   		 		
   		 }).catch(function (error) {
          		console.log(error)
        	})
  		}
    	
  	}
  }
}
</script>

<style>
	.real{width: 100%;overflow: hidden;}
	.real li{width: 100%;height: 3.9rem;border-bottom: 0.05rem solid #d6d6d6;line-height: 3.9rem;overflow: hidden;}
	.real li p{width: 5.5rem;text-align: center;float: left;margin-left: 1.25rem;margin-right: 2.3rem;}
	.real li input{width: 60%;height: 100%;float: left;border:0;outline: none;}
	.real li:last-child{border-bottom: none;}
	.realshow{margin-left: 1.25rem;font-size: 1rem;color: #adadad;text-align: left;}
	.realbtn{width: 61.38%;height: 3.3rem;line-height: 3.3rem;font-size: 1.3rem;background: #ec3c08;border: 0;border-radius: 0.15rem;margin-top: 2rem;color: #FFFFFF;outline: none;}
</style>


