<template>
	<div>
		<scroller style="margin-top: 7rem;margin-bottom: 0rem;" :on-refresh="refresh" :on-infinite="infinite">
					<ul class="press">
						<li v-for="item in deli">
							<router-link :to="{ name:'details', params: {id: item.id} }">
							<p>{{item.title}}</p>
							<span>{{item.create_times}}</span>
							</router-link>
						</li>
					</ul>
		</scroller>
	</div>
</template>

<script>
	import axios from 'axios';
	export default {
		data(){
			return{
			deli:[],
        	offset : 0,
        	
			}
		},methods:{
			getData(){
				var api=this.$root.aurl+'api/index/newgglist';
     		axios.get(api).then((res)=>{
        			this.deli= res.data.data
        			console.log(res)
        		})
			},
			getData1(offset,done){
				var api=this.$root.aurl+'api/index/newgglist';
     		axios.get(api,{params:{'page':offset}}).then((res)=>{
        			if(res.data.data == null ||res.data.data == ''){
        				this.searchvaldata = this.searchvaldata;
        				done(true)
        			}else{
        			if(res.data.data.length < 10 ){
        				this.offset = 0
        				done(true)
        				
        			}else{
        				if(done){
        				done()
        				}
        				}
        			if(offset === 1){
        				this.deli = res.data.data
        			}else{
        				this.deli = this.deli.concat(res.data.data);
        			}	}
        		})
			},infinite (done) {
    			this.offset++    //每当向上滑动的时候就让页数加1
    			this.getData1(this.offset,done)
  			},
  				refresh (done) {
        		//这是向下滑动的时候请求最新的数据 
        		this.getData1(done) 
        		done(true)
        	}
		},mounted(){
			this.getData()
		}
		}
</script>

<style>
	.press{width: 100%;overflow: hidden;}
	.press li{width: 90%;overflow: hidden;margin: 0.6rem auto 0;height: 3rem;line-height: 3rem;border-bottom: 0.05rem solid #eee;}
	.press li a{width: 100%;display: block;overflow: hidden;}
	.press li a p{font-size: 1.4rem;float: left;color: #666;}
	.press li a span{float: right;color: #666;font-size: 1.4rem;}
</style>


