<template>
	<div>
		<scroller style="margin-top: 11rem;margin-bottom: 0rem;" :on-refresh="refresh" :on-infinite="infinite">
			<div class="moneytablemain" >
					<ul v-for="item in deli">
						<li style="width: 20%;">{{item.record_time}}</li>
						<li style="width: 20%;">{{item.type}}</li>
						<li style="width: 35%;">{{item.record_info}}</li>
						<li style="width: 25%;">{{item.record_affect}}</li>
					</ul>
				</div>
		</scroller>
	</div>
</template>

<script>
	import axios from 'axios';
	export default {
		data(){
			return{
			deli:[],
        	offset : 0,
			}
		},methods:{
			getData(){
				var api=this.$root.aurl+'api/user/record';
     		axios.get(api,{params:{'time':3}}).then((res)=>{
        			this.deli= res.data.data
        		})
			},
			getData1(offset,done){
				var api=this.$root.aurl+'api/user/record';
     		axios.get(api,{params:{'page':offset,'time':3}}).then((res)=>{
        			if(res.data.data == null ||res.data.data == ''){
        				this.searchvaldata = this.searchvaldata;
        				done(true)
        			}else{
        			if(res.data.data.length < 10 ){
        				this.offset = 0
        				done(true)
        				
        			}else{
        				if(done){
        				done()
        				}
        				}
        			if(offset === 1){
        				this.deli = res.data.data
        			}else{
        				this.deli = this.deli.concat(res.data.data);
        			}	}
        		})
			},infinite (done) {
    			this.offset++    //每当向上滑动的时候就让页数加1
    			this.getData1(this.offset,done)
  				},refresh (done) { 
        		//这是向下滑动的时候请求最新的数据 
        		this.getData1(done) 
        		done(true)
        	}
		},mounted(){
			this.getData()
		}
		}
</script>

<style>
</style>


