import Vue from 'vue'
import Router from 'vue-router'
import store from '../store/index';
import index from '@/components/index'
import buy from '@/components/buy'
import optional from '@/components/optional'
import quotation from '@/components/quotation'
import user from '@/components/user'
import data from '@/components/data'
import real from '@/components/real'
import bank from '@/components/bank'
import password from '@/components/password'
import mestrategy from '@/components/mestrategy'
import mestrategy1 from '@/components/mestrategy1'
import money from '@/components/money'
import about from '@/components/about'
import recharge from '@/components/recharge'
import withdrawal from '@/components/withdrawal'
import login from '@/components/login'
import search from '@/components/search'
import search1 from '@/components/search1'
import competition from '@/components/competition'
import cattle from '@/components/cattle'
import information from '@/components/information'
import capital from '@/components/capital'
import recharge1 from '@/components/recharge1'
import news from '@/components/news'
import press from '@/components/press'
import payment from '@/components/payment'
import details from '@/components/details'
import zcxy from '@/components/zcxy'
import strategy from '@/components/strategy'
import stock from '@/components/stock'


Vue.use(Router)

const router =  new Router({
  routes: [
    {path: '/',name: 'index',component: index},
    { path: '/index',name: 'index', component: index,meta: {allowBack: false}},
    { path: '/buy/:id',name: 'buy', component: buy,
     meta: {
              requireAuth: true,  
       }
    },
    { path: '/quotation',name: 'quotation', component: quotation,
    	meta: {
              requireAuth: true,  
       }
    },
    { path: '/optional',name: 'optional', component: optional},
    { path: '/user',name: 'user', component: user,
    meta: {
              requireAuth: true,  
       }
    },
    { path: '/data',name: 'data', component: data},
    { path: '/real',name: 'real', component: real},
    { path: '/bank',name: 'bank', component: bank},
    { path: '/password',name: 'password', component: password},
    { path: '/mestrategy',name: 'mestrategy', component: mestrategy},
    { path: '/mestrategy1',name: 'mestrategy1', component: mestrategy1},
    { path: '/money',name: 'money', component: money},
    { path: '/about',name: 'about', component: about},
    { path: '/recharge',name: 'recharge', component: recharge},
    { path: '/withdrawal',name: 'withdrawal', component: withdrawal},
    { path: '/login',name: 'login', component: login},
    { path: '/search',name: 'search', component: search},
    { path: '/search1',name: 'search1', component: search1},
    { path: '/competition',name: 'competition', component: competition},
    { path: '/capital',name: 'capital', component: capital},
    { path: '/recharge1',name: 'recharge1', component: recharge1},
    { path: '/news',name: 'news', component: news},
    { path: '/press',name: 'press', component: press},
    { path: '/payment',name: 'payment', component: payment},
    { path: '/strategy',name: 'strategy', component: strategy},
    { path: '/details/:id',name: 'details',component: details},
    { path: '/zcxy/:id',name: 'zcxy',component: zcxy},
    { path: '/stock/:id',name: 'stock',component: stock},
    { path: '/cattle',name: 'cattle', component: cattle,
    	meta: {
              requireAuth: true,  
       }
    },
    { path: '/information/:id',name: 'information', component: information,
    	meta: {
              requireAuth: true,  
       }
    }
  ]
})

// 注册全局钩子用来拦截导航
if (sessionStorage.getItem('token')) {
store.commit('SET_TOKEN', sessionStorage.getItem('token'))
}
router.beforeEach((to, from, next) => {
  const token = store.state.token
  if (to.matched.some(r => r.meta.requireAuth)) { // 判断该路由是否需要登录权限
    if (store.state.token) { // 通过vuex state获取当前的token是否存在
      next()
    } else {
      next({
        path: '/login',
        query: {redirect: to.fullPath} // 将跳转的路由path作为参数，登录成功后跳转到该路由
      })
    }
  } else {
    next()
  }
})


export default router


