<template>
	<div>
		<div class="header">
			<router-link to="/" slot="left">
			    <mt-button icon="back"></mt-button>
  			</router-link>
  			<ul>
  				<li class="select" @click="switchTo('/recharge')">线上充值</li>
  				<li  @click="switchTo('/recharge1')">线下充值</li>
  			</ul>
		</div>
		<div class="reg">
			<p>充值金额</p>
			<div class="clear"></div>
			<div class="regge">
				<p>￥</p>
				<input type="" placeholder="输入金额" value="" v-model="input"/>
				<img @click="qingkong" src="../assets/cha.png" />
			</div>
			<p>充值方式</p>
			<div class="clear"></div>
			<div class="regge1">
				<ul>
					<li></li>
					<li></li>
					<li></li>
				</ul>
			</div>
			<button class="regbtn" @click="regbtn()">确认充值</button>
		</div>
	</div>
</template>

<script>
	import axios from 'axios'
	export default {
		data(){
			return{
				input:''
			}
		},methods:{
			regbtn:function(){
				var input = this.input;
				if(input == ''){
					this.$toast.center('请输入充值金额');
				}
			},
			qingkong:function(){
				this.input = '';
			},switchTo(path){
      			this.$router.push(path)
   			},regbtn(){
   				var qs = require('qs');
                    var token =sessionStorage.getItem('token');
                    var api=this.$root.aurl+'api/user/payonline';
                    var input = this.input
   		 			window.location.href = this.$root.aurl+'api/user/payonline?money='+ input + '&token=' + token;
   		 			
                    
   			}
			
		}
	}
</script>

<style>
	.header{width: 100%;height: 4.1rem;background: #ec3c08;overflow: hidden;}
	.header .mint-button{background: none;float: left;color: #FFFFFF;height:4.1rem;border: 0;box-shadow: none;}
	.header ul{width: 40.69%; height: 2.75rem;border: 0.05rem solid #FFFFFF;margin: 0.675rem auto;border-radius: 0.6rem;overflow: hidden;}
	.header li{float: left;width: 50%;line-height: 2.75rem;font-size: 1.2rem;color:#ffffff;}
	.header .select{background: #FFFFFF;color: #ec3c08;}
	.reg > p{float: left;font-size: 1.3rem;color: #1a1e22;margin-top: 1.3rem;margin-left: 2.35rem;}
	.regge{width: 88.75%;height: 2.5rem;border-bottom: 0.05rem solid #9a9a9a;margin: 2.1rem auto 0;}
	.regge p{float: left;line-height: 2.5rem;}
	.regge input{height: 2rem;margin-top: 0.25rem;border: 0;outline: none;float: left;width: 70%;margin-left: 1rem;}
	.regge img{width: 1.15rem;height: 1.15rem;margin-top: 0.675rem;margin-left: 1rem;}
	.regbtn{width: 29.86%;height: 3.75rem;line-height: 3.75rem;font-size: 1.3rem;color: #FFFFFF;background: #ec3c08;margin: 4.5rem auto 0;border-radius: 0.75rem;border: 0;outline: none;}
</style>


