<template>
	<div>
		<mt-header title="牛人跟股">
		  <router-link to="/index" slot="left">
		    <mt-button icon="back"></mt-button>
		  </router-link>
		</mt-header>
		<div class="cattletop">
			
			<ul>
				<li v-for="item in rsslist">
					<img :src="$root.aurl+item.head_img"/>
					<h3>{{item.vip_name}}</h3>
					<p>总收益：<span>{{item.sumrate}}%</span></p>
					<router-link :to="{name:'information', params: {id: item.other_id} }">TA的战绩</router-link>
				</li>
			</ul>
			
		</div>
		<mt-navbar v-model="selected">
		  <mt-tab-item id="1">策略持仓</mt-tab-item>
		  <mt-tab-item id="2">牛气排名</mt-tab-item>
		</mt-navbar>
		<mt-tab-container v-model="selected">
		  <mt-tab-container-item id="1">
		  	<ul class="cattlemain1">
		  		<li v-for="item in bestlist">
		  			<img class="cathead1" :src="$root.aurl+item.head_img" />
		  			<dl>
		  				<dt>{{item.vip_phone}}</dt>
		  				<dd>{{item.sell_times}}</dd>
		  			</dl>
		  			<dl>
		  				<dt>{{item.gupiao_name}}</dt>
		  				<dd>{{item.gupiao_code}}</dd>
		  			</dl>
		  			<dl>
		  				<dt>收益率：<span>{{item.syl}}%</span></dt>
		  				<dd>股票量：<span>{{item.trush_number}}</span></dd>
		  			</dl>
		  			<router-link class="sc-data" :to="{ name:'buy',path:'/buy', params: {id: item.gupiao_code} }" >跟买</router-link>
		  		</li>
		  		
		  	</ul>
		  </mt-tab-container-item>
		  <mt-tab-container-item id="2">
		  	<ul class="cattlemain2">
		  		<li v-for="(item,index) in niulist">
		  			<img v-if="index == 0" class="catlist" src="../assets/No1.png"/>
		    		<img v-else-if="index == 1" class="catlist" src="../assets/No2.png"/>
		    		<img v-else-if="index == 2" class="catlist" src="../assets/No3.png"/>
		    		<img v-else class="catlist" src="../assets/Noall.png"/>
		  			<img class="cathead" :src="$root.aurl+item.head_img" />
		  			<ol>
		  				<li>{{item.vip_name}}</li>
		  				<li v-if="item.rss_status == 1" @click="subscribe(item.user_id)"><button>已订阅</button></li>
		  				<li v-else><button @click="subscribe(item.user_id)" class="subscribe">订阅</button></li>
		  			</ol>
		  			<dl>
		  				<dt>总收益率：<span>{{item.profits_rate}}%</span></dt>
		  				<dd>交易次数：<span>{{item.countid}}次</span></dd>
		  			</dl>
		  		</li>
		  		
		  	</ul>
		  </mt-tab-container-item>
		</mt-tab-container>
	</div>
</template>

<script>
	import axios from 'axios';
	import $ from 'jquery';
	export default {
		data() {
			return{
				selected: '1',
      			valuezd:false,
      			bestlist:[],
      			niulist:[],
      			rsslist:[],
			}	
			},methods:{
				getData(){
					var api=this.$root.aurl+'api/user/ace';
					axios.post(api).then((res)=> {
						console.log(res)
						this.bestlist = res.data.data.bestlist;
						this.niulist = res.data.data.niulist;
						this.rsslist = res.data.data.rsslist;
					})
				},subscribe(e){
					var qs = require('qs');
					console.log(e)
					var api=this.$root.aurl+'api/user/setRss';
					axios.post(api,qs.stringify({'id':e})).then((res)=> {
						//console.log(res)
						this.getData();
					})
				},switchTo(path){
      				this.$router.push(path)
    			}
				
			},mounted(){
				this.getData();
				
				
			}
		
	}
</script>

<style>
	.cattletop{
		width: 100%;
		overflow: hidden;
		margin-top: 0.7rem;
	}
	.cattletop li{
		width: 9rem;
		margin-left: 1rem;
		height: 9.8rem;
		float: left;
		border: 0.05rem solid #e3e3e3;
		margin-bottom: 0.5rem;
	}
	.cattletop li img{
		width: 3.35rem;
		height: 3.35rem;
		margin-top: 0.65rem;
		border-radius: 50%;
	}
	.cattletop li h3{
		font-size: 1.2rem;
		color: #000000;
		margin-top: 0.3rem;
	}
	.cattletop li p{
		font-size: 1.1rem;
		color: #6f6f6f;
		margin-top: 0.3rem;
	}
	.cattletop li span{
		color: #ec3e2e;
	}
	.cattletop li a{
		font-size: 1.1rem;
		color: #4f8eff;
		text-decoration: none;
		margin-top: 0.3rem;
		display: block;
	}
	.mint-navbar .mint-tab-item.is-selected{
		color: #000000;
		border-bottom: 0.15rem solid rgba(236,63,37,0.99);
	}
	.cattlemain1 > li{
		border-bottom: 0.05rem solid #bcbcbc;
		width: 100%;
		height: 6.4rem;
		overflow: hidden;
	}
	.cattlemain2 > li{
		border-bottom: 0.05rem solid #bcbcbc;
		width: 100%;
		height: 6.4rem;
		overflow: hidden;
	}
	.catlist{float: left;width: 2.3rem;height: 3.1rem;margin-left: 1.4rem;margin-top: 1.6rem;}
	.cathead{float: left;width: 4.3rem;height: 4.3rem;margin-left: 2.1rem;margin-top: 0.75rem;border-radius: 50%;}
	.cathead1{float: left;width: 4.3rem;height: 4.3rem;margin-left: 1rem;margin-top: 0.75rem;border-radius: 50%;}
	.cattlemain1 ol{float: left;margin-top: 1.1rem;margin-left: 2.1rem;}
	.cattlemain2 ol{float: left;margin-top: 1.1rem;margin-left: 2.1rem;}
	.cattlemain1 li{font-size: 1.3rem;color: #4c4c4c;}
	.cattlemain2 li ol button{width: 4.5rem;height: 2.15rem;font-size: 1.2rem;background: #f5a194;color: #FFFFFF;outline: none;border-radius: 0.25rem;border: 0;margin-top: 0.5rem;}
	.cattlemain1 dl{float: left;margin-top: 1rem;color: #929292;font-size: 1.2rem;margin-left: 0.5rem;line-height: 2rem;}
	.cattlemain1 dl span{color: #ec4229;}
	
	.cattlemain2 li{font-size: 1.3rem;color: #4c4c4c;}
	
	.cattlemain2 dl{float: left;margin-top: 1rem;color: #929292;font-size: 1.2rem;margin-left: 3rem;line-height: 2rem;}
	.cattlemain2 dl span{color: #ec4229;}
	.cattlemain1 >li a{margin-left:1rem;width: 4.5rem;outline: none;height: 2.15rem;line-height: 2.15rem;border: 0.05rem solid #ec4229;background: none;color: #ec4229;margin-top: 2rem;border-radius: 0.25rem;display: block;float: left;text-decoration: none;}
	.cattlemain2 li .subscribe{background: #ec4229;}
</style>


