<template>
	<div>
		<mt-swipe :auto="4000" class="banner" style="height: 14rem;">
		  <mt-swipe-item v-for="item in banner"><img :src="$root.aurl+item.img"></mt-swipe-item>
		</mt-swipe>
		<div class="index-show">
			<ul>
				<li>
					<img src="../assets/index-ico1.png" />
					<span>灵活便捷</span>
				</li>
				<li>
					<img src="../assets/index-ico2.png" />
					<span>超低门槛</span>
				</li>
				<li>
					<img src="../assets/index-ico3.png" />
					<span>智能选股</span>
				</li>
				<li>
					<img src="../assets/index-ico4.png" />
					<span>牛股跟股</span>
				</li>
				
			</ul>
		</div>
		<div class="index-show2">
			<ul>
				<li @click="switchTo('/cattle')">
					<dl>
						<dt>牛人跟股</dt>
						<dd>有效决策/精准优选</dd>
					</dl>
					<img src="../assets/index-show21.png" />
				</li>
				<!--<li @click="switchTo('/competition')">
					<dl>
						<dt>炒股大赛</dt>
						<dd>实盘行情/模拟牛股</dd>
					</dl>
					<img src="../assets/index-show22.png" />
				</li>-->
				<li>
					<dl>
						<dt>炒股大赛</dt>
						<dd>实盘行情/模拟牛股</dd>
					</dl>
					<img src="../assets/index-show22.png" />
				</li>
				
				
			</ul>
		</div>
		<div class="clear"></div>
		<mt-navbar v-model="selected">
		  <mt-tab-item id="1">策略排行</mt-tab-item>
		  <mt-tab-item id="2">牛气达人</mt-tab-item>
		</mt-navbar>
		<mt-tab-container v-model="selected">
		  <mt-tab-container-item id="1" >
		    <div class="ranking" v-for="item in ranking">
		    	<div class="rankingtop">
		    	<dl>
		    		<dt>股票数<span>{{item.trush_number}}</span></dt>
		    		<dd>收益率<span class="red">{{item.syl}}%</span></dd>
		    	</dl>
		    	<dl>
		    		<dt>买入价<span>{{item.trush_price}}</span></dt>
		    		<dd>当前价<span class="red">{{item.now_price}}</span></dd>
		    	</dl>
		    	<dl>
		    		<dt>止盈价<span>{{item.stop_win}}</span></dt>
		    		<dd>止损价<span class="green">{{item.stop_down}}</span></dd>
		    	</dl>
		    	</div>
		    	<div class="clear"></div>
		    	<div class="rankingmain">
		    		<img :src="$root.aurl+item.head_img" />
		    		<dl>
		    			<dt>{{item.vip_phone}}</dt>
		    			<dd>{{item.sell_times}}</dd>
		    		</dl>
		    		<dl>
		    			<dt>{{item.gupiao_name}}</dt>
		    			<dd>{{item.gupiao_code}}</dd>
		    		</dl>
		    		<router-link class="sc-data" :to="{ name:'buy',path:'/buy', params: {id: item.gupiao_code} }" >跟买</router-link>
		    	</div>
		    </div>  
		  </mt-tab-container-item>
		  <mt-tab-container-item id="2">
		    	<div class="rankingtable" >
		    		<table>
		    			<tr v-for="(item,index) in ranking2">
		    				<td v-if="index == 0"><img src="../assets/No1.png"/></td>
		    				<td v-else-if="index == 1"><img src="../assets/No2.png"/></td>
		    				<td v-else-if="index == 2"><img src="../assets/No3.png"/></td>
		    				<td v-else><img src="../assets/Noall.png"/></td>
		    				<td>
		    					<dl>
		    						<dt>{{item.vip_name}}</dt>
		    						<dd><a>已订阅</a></dd>
		    					</dl>
		    				</td>
		    				<td>
		    					<ul>
		    						<li>总收益率<span>{{item.syl}}%</span></li>
		    						<li>交易次数<span>6次</span></li>
		    					</ul>
		    				</td>
		    			</tr>
		    		</table>
		    	</div>  
		  </mt-tab-container-item>
		</mt-tab-container>
		<mi-footer></mi-footer>
	</div>
</template>

<script>
	import footer from './footer';
	import axios from 'axios'
	export default {
  		name: 'page-navbar',
	components: {
      'mi-footer': footer
    },
  data() {
    return {
      selected: '1',
      banner:[],
      ranking:[],
      ranking2:[]
    };
  },methods:{
  		getData(){
                    //请求数据
                    var api=this.$root.aurl+'api/index';
                    axios.get(api).then((res)=>{
   		 					//console.log(res)	
   		 				this.banner = res.data.data.banner;
   		 			//	this.ranking = res.data.data.trade;
   		 			})
                    
           },getData1(){
           	var api=this.$root.aurl+'api/index';
           	axios.get(api,{params:{type:'trade'}}).then((res)=>{
   		 				//	console.log(res)	
   		 				this.ranking = res.data.data.trade;
   		 		})
           },getData3(){
           	var api=this.$root.aurl+'api/index';
           	axios.get(api,{params:{type:'vip'}}).then((res)=>{
   		 				//	console.log(res)	
   		 				this.ranking2 = res.data.data.trade;
   		 		})
           },switchTo(path){
      			this.$router.push(path)
   			}
  },mounted(){
  		this.getData()
  		this.getData1()
  		this.getData3()
  }
}
</script>

<style>
	
	.mint-swipe-item img{
		width: 100%;
		height: 100%;
	}
	.index-show{
		width: 100%;
		height: 8rem;
	}
	.index-show li{
		width: 25%;
		float: left;
	}
	.index-show li img{
		width: 4rem;
		height: 4rem;
		display: block;
		margin: 1rem auto 0;
	}
	.index-show li span{
		font-size: 1.2rem;
		line-height: 3rem;
		color: #000000;
	}
	.index-show2{
		width: 100%;
		height: 7rem;
	}
	.index-show2 li{
		width: 50%;
		float: left;
	}
	.index-show2 li dl{
		float: left;
		text-align: left;
		margin-left: 1rem;
	}
	.index-show2 li dt{
		font-size: 1.4rem;
		color: #ec4449;
		margin-top: 2rem;
	}
	.index-show2 li dd{
		font-size: 1.1rem;
		color: #afaeaf;
		
	}
	.index-show2 li img{
		float: right;
		width: 38%;
		margin-top: 1.3rem;
	}
	
	.ranking{
		width: 100%;
		border-bottom: 0.1rem solid #c8c8c8;
		overflow: hidden;
		padding-bottom: 0.55rem;
		
	}
	.rankingtop dl{
		margin-top: 1.1rem;
		float: left;
		width: 27.64%;
		text-align: left;
		margin-left: 5.69%;
		line-height: 1.85rem;
		font-size: 1.3rem;
		color: #adaeae;
	}
	.rankingtop dl span{
		color: #3d3d3d;
	}
	.rankingtop .red{
		color: #f35358;
	}
	.rankingtop .green{
		color: #01af1a;
	}
	.rankingmain{
		width: 100%;
		margin-top: 1rem;
	}
	.rankingmain img{
		float: left;
		width: 3.8rem;
		height: 3.8rem;
		margin-left: 5.42%;
		border-radius: 50%;
	}
	.rankingmain dl{
		float: left;
		margin-left: 5%;
		margin-right: 5%;
		margin-top: 0.7rem;
	}
	.rankingmain dt{
		font-size: 1.3rem;
		color: #5d5d5e;
	}
	.rankingmain dd{
		font-size: 1.1rem;
		color: #adaeae;
	}
	.rankingmain a{
		width: 4.75rem;
		height: 2.35rem;
		line-height: 2.35rem;
		border: 0.05rem solid #ed4e53;
		border-radius: 0.55rem;
		background: none;
		text-decoration: none;
		display: block;
		float: right;
		font-size: 1.2rem;
		color: #e91c23;
		margin-top: 0.7rem;
		margin-right: 10%;
	}
	.rankingtable table{width: 100%;margin-top: 0.5rem;}
	.rankingtable td{padding: 0.5rem 0;}
	.rankingtable dl,.rankingtable li{line-height: 1.5rem;}
	.rankingtable dd a{padding: 0.2rem 0.6rem;background: #f5a194;font-size: 1.2rem;color: #FFFFFF;border-radius: 0.2rem;}
	.rankingtable li span{color: #ed4f37;margin-left: 0.3rem;}
</style>


