<template>
	<div>
	<canvas id="canvas" width="414" height="310" style="z-index: 3; background: transparent;">
        <p>你的浏览器不支持html5哟</p>
   </canvas>
 	<p id="mins" style="display: none">{{mins}}</p>
    </div>
</template>

<script>
	import $ from 'jquery';
	import axios from 'axios';
	export default {
		data(){
			return{
				mins:[],
			}
		},methods:{
			getminsData(id){
   			var api=this.$root.aurl+'api/index/minute_k?code='+id;
   			axios.get(api).then((res)=>{
   				//console.log(res)
   					  this.mins = res.data.data		 
   			})
   		
   		}
		},mounted(){
			var id=this.$route.params.id
			this.getminsData(id)
			
			
			
		},updated(){
			$.getScript("../../../static/mins-data.js")
			$.getScript("../../../static/k.js")
			
		}
	}
	
	
			
	
</script>

<style>
	#canvas{
		width: 100%;
	}
	
</style>


