<template>
	<div>
		<mt-header title="关于我们 ">
		  <router-link to="/user" slot="left">
		    <mt-button icon="back"></mt-button>
		  </router-link>
		</mt-header>
		<div class="about">
			<div class="aboutmain" v-html="content">{{content}}</div>
		</div>
	</div>
</template>

<script>
	import axios from 'axios';
	export default {
		data(){
    	return{
    		title:'',
    		content:''
    	}
		},methods:{
			getList(){
			var api=this.$root.aurl+'api/index/about';
        	let token =sessionStorage.getItem('token');
        	axios.get(api,{params:{'key':token}}).then((res)=>{
        		//	console.log(res)
        			this.title=res.data.data.title
        			this.content=res.data.data.content
        		})}
		},mounted(){
			this.getList()
		}
	}
</script>

<style>
	.about h3{
		width: 90%;
		margin: 0.3rem auto;
		font-size: 1.4rem;
		font-weight: 500;
	}
	.aboutmain{
		width: 90%;
		margin: 0.3rem auto 0;
		font-size: 1.2rem;
		text-align: left;
	}
</style>


