<template>
	<div>
	<div class="header">
			<router-link to="/optional" class="left">
			    <mt-button icon="back"></mt-button>
  			</router-link>
  			<p v-html="title">{{title}}</p>
  			<router-link to="/search1" class="right">
			    <mt-button icon="search"></mt-button>
  			</router-link>
		</div>
	<div class="stock">
		<div class="positions">
		<div class="stockfirst">
			<dl :class="item.price_range > 0?'red':'green'">
				<dt>{{item.current_price}}</dt>
				<dd>{{item.price_range}}&nbsp;&nbsp;&nbsp;&nbsp;{{item.price_rate}}%</dd>
			</dl>
			<ul>
				<li>今开<span :class="item.open_price > item.yesterday_price?'red':'green'">{{item.open_price}}</span></li>
				<li>昨收<span>{{item.yesterday_price}}</span></li>
			</ul>
			<ul>
				<li>最高<span>{{item.highest}}</span></li>
				<li>最低<span>{{item.lowest}}</span></li>
			</ul>
		</div>
		<div class="stocktwo">
			<ul>
				<li>成交量<span>{{(Math.round(item.volume/100)/100).toFixed(2)}}万</span></li>
				<li>成交额<span>{{(Math.round(item.turnover/100)/100).toFixed(2)}}亿</span></li>
			</ul>
			<ul>
				<li>市盈率<span>{{item.pe_ratio}}</span></li>
				<li>市净率<span>{{item.pb_ratio}}</span></li>
			</ul>
			<ul>
				<li>换手率<span>{{item.turnover_rate}}%</span></li>
				<li>总市值<span>{{item.total_market_value}}亿</span></li>
			</ul>
			
		</div>
		</div>
		<div class="yinyong">
			
		<iframe  :src="ifram" frameborder="0" scrolling="no" style="position: absolute; top: 0; left: 0; width: 100%; height: 574px; margin-top: -160px;"></iframe>
			
		</div>
		<!--<div class="inner-k">
			<ul>
				<li @click="tabsSwitch()"  class="chenck"><p>分时</p></li>
				<li @click="tabsSwitch1()"><p>日k</p></li>
				<li @click="tabsSwitch2()"><p>周k</p></li>
				<li @click="tabsSwitch3()"><p>月k</p></li>
			</ul>
		</div>
			<mi-timek v-if='showminute'></mi-timek>
			<mi-dayk v-if='showday'></mi-dayk>
			<mi-weekk v-if='showweek'></mi-weekk>
			<mi-monthk v-if='showmonth'></mi-monthk>-->
		<div class="stockthree">
			<div class="stockleft">
				<p>买盘档</p>
				<ul>
					<li>
						<span>买一</span>
						<samp>{{item.buy_one_price}}</samp>
						<p>{{item.buy_one_amount}}</p>
					</li>
					<li>
						<span>买二</span>
						<samp>{{item.buy_two_price}}</samp>
						<p>{{item.buy_two_amount}}</p>
					</li>
					<li>
						<span>买三</span>
						<samp>{{item.buy_three_price}}</samp>
						<p>{{item.buy_three_amount}}</p>
					</li>
					<li>
						<span>买四</span>
						<samp>{{item.buy_four_price}}</samp>
						<p>{{item.buy_four_amount}}</p>
					</li>
					<li>
						<span>买五</span>
						<samp>{{item.buy_five_price}}</samp>
						<p>{{item.buy_five_amount}}</p>
					</li>
				</ul>
			</div>
			<div class="stockright">
				<p>卖盘档</p>
				<ul>
					<li>
						<span>卖一</span>
						<samp>{{item.sell_one_price}}</samp>
						<p>{{item.sell_one_amount}}</p>
					</li>
					<li>
						<span>卖二</span>
						<samp>{{item.sell_two_price}}</samp>
						<p>{{item.sell_two_amount}}</p>
					</li>
					<li>
						<span>卖三</span>
						<samp>{{item.sell_three_price}}</samp>
						<p>{{item.sell_three_amount}}</p>
					</li>
					<li>
						<span>卖四</span>
						<samp>{{item.sell_four_price}}</samp>
						<p>{{item.sell_four_amount}}</p>
					</li>
					<li>
						<span>卖五</span>
						<samp>{{item.sell_five_price}}</samp>
						<p>{{item.sell_five_amount}}</p>
					</li>
				</ul>
			</div>
		</div>
	</div>
	<div class="stockfooter">
		<button class="cancel" @click="optional()" >{{sdata1}}</button>
		<router-link :to="{ name:'strategy'}" style="background: #05AA3B;color: #FFFFFF;">卖出</router-link>
		<router-link :to="{ name:'buy', params: {id:code} }" style="background: #FF4500;color: #FFFFFF;">买入</router-link>
	</div>
	<p id="data" style="display: none;">{{item}}</p>
	</div>
</template>

<script>
	import axios from 'axios'
	import timek from './k/timek';
	import dayk from './k/dayk';
	import weekk from './k/weekk';
	import monthk from './k/monthk';
	export default {
		components: {
			
			'mi-timek': timek,
			'mi-dayk': dayk,
			'mi-weekk': weekk,
			'mi-monthk': monthk
		},
		data(){
			return{
				showminute:true,
				showday:false,
				showweek:false,
				showmonth:false,
				title:'',
				name:'',
				code:'',
				item:[],
				sdata1:'',//自选状态
				ifram:''
			}
		},methods:{
			getData(id){
				var api=this.$root.aurl+'api/index/market?code='+id;
				axios.get(api).then((res)=>{
					//console.log(res)
					this.title = res.data.data.name+'<br/>'+res.data.data.code;
					this.name = res.data.data.name
					this.code = res.data.data.code
					this.item=res.data.data;
					this.getput()
					this.ifram = 'http://www.zcmpz.com/wap/trade/stock/'+this.code
				})
			},tabsSwitch(){
   			this.showminute = true;
   			this.showday = this.showweek = this.showmonth = false;
   		},tabsSwitch1(){
   			this.showday = true;
   			this.showminute = this.showweek = this.showmonth = false;
   		},tabsSwitch2(){
   			this.showweek = true;
   			this.showminute = this.showday = this.showmonth = false;
   		},tabsSwitch3(){
   			this.showmonth = true;
   			this.showminute = this.showday = this.showweek = false;
   		},optional(){
          	var qs = require('qs');
          	var name = this.name;
          	var that =this;
          	var code = this.code;
          	var api=this.$root.aurl+'api/user/add_gprss';
     				axios.post(api,qs.stringify({'gupiao_name':name,'gupiao_code':code})) .then(function(res){
    					//console.log(res)
            				that.$toast.center(res.data.error);   
    				});
    				var id=this.$route.params.id; 
    				this.getData(id);
          },getput(){
          	var qs = require('qs');
          	var that =this;
          	var code = this.code;
          	var api=this.$root.aurl+'api/user/getrss_status';
             axios.post(api,qs.stringify({'gupiao_code':code})) .then(function(res){
    					//console.log(res)
    					if(res.data.code == '200'){
    						that.sdata1 = res.data.data; 
    					}else{
    						that.sdata1 = res.data.data;
    					}
    		});
          }
		},mounted(){
			 var id=this.$route.params.id;
			 this.getData(id);
			 this.getput()
			 $(".inner-k li").on('click',function(){
				$(".inner-k li").eq($(this).index()).addClass('chenck').siblings().removeClass('chenck');
		});
		}
	}
</script>

<style>
	.header{width: 100%;height: 4.1rem;background: #ec3c08;overflow: hidden;display: flex;z-index: 9;position: relative;}
	.header .mint-button{background: none;color: #FFFFFF;height:4.1rem;border: 0;box-shadow: none;}
	.header p{flex:1 1 100%;text-align: center;padding: 0.5rem;font-size: 1.4rem;color: #FFFFFF;}
	.header .left{flex:1 1 100%;display: flex;justify-content:flex-start}
	.header .right{flex:1 1 100%;display: flex;justify-content:flex-end}
	.stockfirst{width: 100%;overflow: hidden;background: #FFFFFF;}
	.stockfirst dl{float: left;width: 40%;text-align: center;margin-top: 1rem;}
	.stockfirst dt{font-size: 1.6rem;font-weight: 600;}
	.stockfirst dd{font-size: 1.3rem;}
	.stockfirst ul{float: left;width: 30%;margin-top: 1rem;font-size: 1.3rem;text-align: center;color: #8e8e93;}
	.stockfirst ul li span{color: #000;margin-left: 0.3rem;}
	.stocktwo{border-top: 1px solid #f1f1f1;margin-top: 0.5rem;width: 100%;overflow: hidden;padding:0.8rem 0;background: #FFFFFF;}
	.stocktwo ul{width: 33.33%;float: left;}
	.stocktwo li{font-size: 1.3rem;color: #8e8e93;text-align: left;padding-left: 10%;line-height: 1.5rem;}
	.stocktwo li span{color: #000000;}
	.stockthree{width: 100%;overflow: hidden;margin-bottom: 4.3rem;background: #FFFFFF;position: relative;z-index: 9;}
	.stockleft{width: 46%;float: left;margin-left: 2%;}
	.stockleft > p{width: 100%;height: 3.5rem;line-height: 3.5rem;font-size: 1.4rem;}
	.stockleft li{width: 100%;height: 2rem;line-height: 2rem;color: #8e8e93;}
	.stockleft li span{width: 30%;float: left;font-size: 1.3rem;display: block;}
	.stockleft li samp{width: 30%;float: left;font-size: 1.3rem;display: block;color: #ff4500;}
	.stockleft li p{width: 40%;float: left;font-size: 1.3rem;}
	.stockright{width: 46%;float: left;margin-left: 2%;}
	.stockright > p{width: 100%;height: 3.5rem;line-height: 3.5rem;font-size: 1.4rem;}
	.stockright li{width: 100%;height: 2rem;line-height: 2rem;color: #8e8e93;}
	.stockright li span{width: 30%;float: left;font-size: 1.3rem;display: block;}
	.stockright li samp{width: 30%;float: left;font-size: 1.3rem;display: block;color: #05AA3B;}
	.stockright li p{width: 40%;float: left;font-size: 1.3rem;}
	.stockfooter{width: 100%;height: 4rem;line-height: 4rem;position:fixed;left: 0;bottom: 0;}
	.stockfooter a{display:block;width: 33.33%;float: left;height: 4rem;background: #FFFFFF;border: none;outline: none;font-size: 1.4rem;color: #000;}
	.stockfooter button{display:block;width: 33.33%;float: left;height: 4rem;background: #FFFFFF;border: none;outline: none;font-size: 1.4rem;color: #000;}
	.inner-k{
		width: 100%;
		height: 4rem;
		line-height: 4rem;
		overflow: hidden;
	}
	.inner-k li{
		float: left;
		width: 25%;
		text-align: center;
	}
	.inner-k li p{
		margin: 0 auto;
		text-align: center;
		font-size: 1.4rem;
	}
	.inner-k .chenck p{
		
		width: 80%;
		height: calc(4rem - 2px);
		border-bottom: 1px solid #FF7300;
	}
	.stockfirst .red{color: #ec3c08;}
	.stockfirst .green{color: #3da32c;}
	.positions{position: relative;z-index: 9;}
	.yinyong{width: 100%;border: none;height: 358px;position: relative;}
</style>


