<template>
	<div>
		<mt-header title="已委托的策略 ">
		  <router-link to="/user" slot="left">
		    <mt-button icon="back"></mt-button>
		  </router-link>
		</mt-header>
		<div class="positionsmain">
		    		<ul>
		    			<li>股票/数量</li>
		    			<li>收益/卖出类型 </li>
		    			<li>买入价/卖出价</li>
		    			<li>买入时间 /卖出时间 </li>
		    		</ul>
		    		<div class="positionssge" v-for="item in celue">
		    			<dl>
		    				<dt><span class="black">{{item.gupiao_name}}</span></dt>
		    				<dd><span>{{item.deal_number}}</span></dd>
		    			</dl>
		    			<dl>
		    				<dt><span class="red">{{item.repay_profits}}</span></dt>
		    				<dd><span class="green">{{item.sell_types}}</span></dd>
		    			</dl>
		    			<dl>
		    				<dt>{{item.now_price}}</dt>
		    				<dd><span class="red">{{item.sell_price}}</span></dd>
		    			</dl>
		    			<dl>
		    				<dt>{{item.create_times}}</dt>
		    				<dd><span class="red">{{item.sell_times}}</span></dd>
		    			</dl>
						<button @click="show1(item.id)">查看</button>
		    		</div>
		    	</div>
		<div class="popup" v-show="isShow">
			<div class="pmain">
				<div class="pmain-title">
					<span>策略详情</span>
					<p @click="hide()">&#215;</p>
				</div>
				<div class="pmain-m">
					<div class="pmain-m1">
						<p><span>交易明细</span></p>
						<ul>
							<li><dl>
								<dt>{{gupiao_name}}</dt>
								<dd>{{gupiao_code}}</dd>
							</dl>
							</li>
							<li><dl>
								<dt>交易单号</dt>
								<dd>{{order_no}}</dd>
							</dl>
							</li>
							<li><dl>
								<dt>股票数量</dt>
								<dd>{{deal_number}}</dd>
							</dl>
							</li>
							<li>
								<dl>
								<dt>卖出类型</dt>
								<dd>{{sell_types}}</dd>
								</dl>
							</li>
							<li>
								<dl>
								<dt>买入价格</dt>
								<dd>{{defer_money}}</dd>
								</dl>
							</li>
							<li>
								<dl>
								<dt>卖出价格</dt>
								<dd>{{sell_price}}</dd>
								</dl>
							</li>
							<li>
								<dl>
								<dt>持仓时间</dt>
								<dd>{{chi_day}}天</dd>
								</dl>
							</li>
							
						</ul>
						<ol>
							<li>
								<dl>
								<dt>买入时间</dt>
								<dd>{{deal_times}}</dd>
								</dl>
							</li>
							<li>
								<dl>
								<dt>卖出时间</dt>
								<dd>{{sell_times}}</dd>
								</dl>
							</li>
						</ol>
					</div>
					<div class="pmain-m2">
						<p><span>资金明细</span></p>
						<ul>
							<li>
								<dl>
								<dt>交易本金</dt>
								<dd>{{all_money}}</dd>
								</dl>
							</li>
							<li>
								<dl>
								<dt>信用金</dt>
								<dd>{{credit_money}}</dd>
								</dl>
							</li>
							<li>
								<dl>
								<dt>交易综合费</dt>
								<dd>{{service_money}}</dd>
								</dl>
							</li>
							<li>
								<dl>
								<dt>续期费用</dt>
								<dd>{{defer_money}}</dd>
								</dl>
							</li>
							<li>
								<dl>
								<dt>返还信用金</dt>
								<dd>{{repay_creat_money}}</dd>
								</dl>
							</li>
							<li>
								<dl>
								<dt>盈亏分配</dt>
								<dd>{{repay_profits}}</dd>
								</dl>
							</li>
						</ul>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import axios from 'axios';
	export default {
	components: {
     
    },
  data() {
    return {
    
     celue:[],
     isShow:false,
     gupiao_name:'',
     gupiao_code:'',
     order_no:'',
     deal_number:'',
     sell_types:'',
     defer_money:'',
     deal_times:'',
     sell_price:'',
     sell_times:'',
     chi_day:'',
     all_money:'',
     credit_money:'',
     service_money:'',
     defer_money:'',
     repay_creat_money:'',
     repay_profits:''
    };
  },methods:{
  	getList(){
  		var api=this.$root.aurl+'api/user/history_trade';
   		 axios.get(api).then((res)=>{
   		 	//console.log(res)
   		 	this.celue = res.data.data.list;
   		 })
  	},
  	show1(v){
  		var api=this.$root.aurl+'api/user/oldinfo';
  		var qs = require('qs');
  		axios.post(api,qs.stringify({'id':v})).then((res)=>{
   		 	//console.log(res)
   		 	//console.log(res.data.code)
   		 	this.gupiao_name = res.data.data.gupiao_name;
   		 	this.gupiao_code = res.data.data.gupiao_code;
   		 	this.order_no = res.data.data.order_no;
   		 	this.deal_number = res.data.data.deal_number;
   		 	this.sell_types = res.data.data.sell_types;
   		 	this.defer_money = res.data.data.defer_money;
   		 	this.deal_times = res.data.data.deal_times;
   		 	this.sell_price = res.data.data.sell_price;
   		 	this.sell_times = res.data.data.sell_times;
   		 	this.chi_day = res.data.data.chi_day;
   		 	this.all_money = res.data.data.all_money;
   		 	this.credit_money = res.data.data.credit_money;
   		 	this.service_money = res.data.data.service_money;
   		 	this.defer_money = res.data.data.defer_money;
   		 	this.repay_creat_money = res.data.data.repay_creat_money;
   		 	this.repay_profits = res.data.data.repay_profits;
   		 })
  		this.isShow = !this.isShow;
  	},hide(){
  		this.isShow = !this.isShow;
  	}
  },mounted(){
   		this.getList();
   		 
   }
}
</script>

<style>
	.positionsmain ul{width: 100%;height: 3rem;line-height: 3rem;overflow: hidden;border-bottom: 0.05rem solid #d3d3d3;}
	.positionsmain li{width: 23%;float: left;font-size: 1rem;color: #838383;}
	.positionsmain li:last-child{width: 31%;}
	.positionssge{width: 100%;overflow: hidden;border-bottom: 0.05rem solid #d3d3d3;padding-bottom: 0.5rem;}
	.positionssge dl{float: left;width: 23%;font-size: 1px;color: #656565;font-size: 1.4rem;}
	.positionssge dl:last-child{width: 31%;}
	.positionssge dl span{color: #b1b0b0;}
	.positionssge dt{margin-top: 0.9rem;margin-bottom: 0.45rem;}
	.positionssge .black{color: #000000;}
	.positionssge .red{color: #ec4229;}
	.positionssge .green{color: #4c9604;}
	.positionssge button{width: 80%;height: 3rem;margin-top: 0.5rem;border: 0.05rem solid #ec4229;background: none;outline: none;color: #ec4229;border-radius: 0.5rem;}
	.popup{width: 100%;height: 100%;position: fixed;background: rgba(0,0,0,0.3);top: 0;left: 0;}
	.pmain{width: 90%;position: absolute;top: 15%;left: 5%;background: #FFFFFF;}
	.pmain-title{width: 100%;height: 3.5rem;background: #fe4b2b;line-height: 3.5rem;}
	.pmain-title span{float: left;font-size: 1.3rem;color: #FFFFFF;margin-left: 1rem;}
	.pmain-title p{float: right;font-size: 2rem;color: #FFFFFF;margin-right: 1rem;}
	.pmain-m1 p{width: 90%;height: 2.5rem;line-height: 2.5rem;font-size: 1.2rem;text-align: left;margin: 0 auto;}
	.pmain-m1 p span{border-left: 0.3rem solid #fea494;padding-left: 0.5rem;}
	.pmain-m1{width: 100%;border-bottom: 0.05rem solid #d3d3d3;overflow: hidden;padding-bottom: 0.5rem;}
	.pmain-m1 ul{width: 90%;margin: 0 auto;overflow: hidden;}
	.pmain-m1 ol{width: 90%;margin: 0 auto;overflow: hidden;}
	.pmain-m1 ul li{float: left;width: 33.33%;line-height: 2rem;font-size: 1.1rem;}
	.pmain-m1 ol li{float: left;width: 50%;line-height: 2rem;font-size: 1.1rem;}
	.pmain-m1 dt{font-size: 1.2rem;color: #6e6e6e;}
	.pmain-m1 dd{font-size: 1.1rem;color: #9e9e9e;}
	.pmain-m2{padding-bottom: 1rem;width: 100%;overflow: hidden;}
	.pmain-m2 p{width: 90%;height: 2.5rem;line-height: 2.5rem;font-size: 1.2rem;text-align: left;margin: 0 auto;}
	.pmain-m2 p span{border-left: 0.3rem solid #fea494;padding-left: 0.5rem;}
	.pmain-m2 ul{width: 90%;margin: 0 auto;}
	.pmain-m2 li{float: left;width: 33.33%;line-height: 2rem;font-size: 1.1rem;}
	.pmain-m2 dt{font-size: 1.2rem;color: #6e6e6e;}
	.pmain-m2 dd{font-size: 1.1rem;color: #9e9e9e;}
</style>


