<template>
	<div>
		<mt-header title="绑定银行卡 ">
		  <router-link to="/data" slot="left">
		    <mt-button icon="back"></mt-button>
		  </router-link>
		</mt-header>
		<div class="bank">
			<ul>
				
				<li>
					<p>卡号</p>
					<input type="text" placeholder="请填写银行卡号" v-model="input1" v-if="input2 == ''"/>
					<input type="text" :value="input2" disabled="disabled" v-else/>
				</li>
				<li>
					<p>所属银行</p>
					<input type="text" placeholder="请填写银行卡所属银行" v-model="input6" v-if="input3 == ''"/>
					<input type="text" :value="input3" disabled="disabled" v-else/>
				</li>
				<li>
					<p>手机号</p>
					<input type="text" disabled="disabled" :value="input4"/>
				</li>
				<li>
					<p>验证码</p>
					<input type="text" placeholder="输入验证码" v-model="input5" style="width: 40%;"/>
					<button @click="dx_yzm_bt"  ref="codetime">获取验证码</button>
				</li>
			</ul>
			<button class="bankbtn" @click="realbtn()">确认</button>
		</div>
		<mi-footer></mi-footer>
	</div>
</template>

<script>
	import footer from './footer';
	import axios from 'axios';
	export default {
  		name: 'page-navbar',
	components: {
      'mi-footer': footer
    },
  data() {
    return {
     input1:'',
     input2:'',
     input3:'',
     input4:'',
     input5:'',
     input6:'',
    };
  },methods:{
  	getData(){
  		var qs = require('qs');
  		let token =sessionStorage.getItem('token');
  		axios.post(this.$root.aurl+'api/user/bank_validate',qs.stringify({'key':token,}))
   		 .then((res)=>{
   		 	console.log(res)
   		 	this.input4 = res.data.data.vip_phone
   		 	this.input3 = res.data.data.bank.bank_name
   		 	this.input2 = res.data.data.bank.bank_number
   		 	console.log(this.input4)
   		 }).catch(function (error) {
          		console.log(error)
        	})
  	},
  	realbtn(){
  		var input1 = this.input1;
  		var input6 = this.input6;
  		var input5 = this.input5;
  		var that = this
  		var qs = require('qs');
  		if(input1 == ''||input6 == ''||input5 == ''){
  			this.$toast.center('信息输入不完整');
  		}else{
  			let token =sessionStorage.getItem('token');
   		axios.post(this.$root.aurl+'api/user/bank_validate',qs.stringify({'key':token,'phone_code':input5,' bank_number':input1,'bank_name':input6}))
   		 .then((res)=>{
   		 	//console.log(res)
   		 	if(res.data.code == '400'){
   		 		that.$toast.center(res.data.error);
   		 	}else{
   		 		that.$toast.center(res.data.data);
   		 	}
   		 	
   		 		
   		 }).catch(function (error) {
          		console.log(error)
        	})
  		}
    	
  	},dx_yzm_bt:function(){
                	var qs = require('qs');
                	var input4 = this.input4
                	const url = this.$root.aurl+"api/base/sendcode"
                	axios.post(url,qs.stringify({'vip_phone':input4})).then((response)=> {
         				if(response.data.status != 1){
         				var tishi = response.data.message
    					this.$toast.center(tishi);
         				}else{
         					this.$toast.center('验证码已发送');
         				}
         				
         				this.mekey = response.data.message;
         			}, function (error) {
         			console.log("请求失败",error);
        			})
      				var num = 60;
      				var codetime = this.$refs.codetime;
      				var timer = setInterval(function () {
      					num-- 
      					codetime.innerHTML = num + '秒后重新获取'
      					codetime.style.color = ' #ec3c08'
      					codetime.disabled = ' disabled'
      					if (num === 0) { 
      						codetime.disabled = ''
      						codetime.style.color = ' #ec3c08'
      						codetime.innerHTML = '获取验证码'
      						clearInterval(timer) } 
      				}, 1000)
      				
    	}
  },mounted(){
  	this.getData()
  }
}
</script>

<style>
	.bank{width:100%;overflow: hidden;}
	.bank li{width: 100%;height: 3.4rem;line-height: 3.4rem;border-bottom: 0.05rem solid #d6d6d6;}
	.bank li p{float: left;margin-left: 2.4rem;width: 4.8rem;font-size: 1.1rem;color: #404040;text-align: left;}
	.bank li input{width: 50%;height: 3rem;margin-top:0.2rem;float: left;margin-left: 2.75rem;border: 0;outline: none;background: none;}
	.bank li button{float: left;font-size: 1rem;color: #ec3c08;background: none;border: none;outline: none;height: 3rem;margin-top: 0.2rem;}
	.bankbtn{width: 61.38%;height: 3.3;line-height: 3.3rem;border-radius: 0.15rem;background: #ec3c08;margin: 6.3rem auto 0;border: 0;color: #FFFFFF;font-size: 1.3rem;outline: none}
</style>


