<template>
	<div>
		<div class="usertop">
			<div class="userhead">
				<img src="../assets/userhead.png" v-if="vip_head == 'uploads/'"/>
				<img :src="$root.aurl+vip_head" v-else/>
			</div>
			<p>{{vipphone}}</p>
			<ul>
				<li><img src="../assets/user01.png"/></li>
				<li v-show="vip_bank > 0"><img src="../assets/user2.png"/></li>
			</ul>
			<div class="clear"></div>
			<div class="usershow">
				<dl>
					<dt>账户资产</dt>
					<dd>{{has_sum}}</dd>
				</dl>
				<dl>
					<dt>持仓盈亏</dt>
					<dd>{{yingkui}}</dd>
				</dl>
				<dl>
					<dt>冻结资产</dt>
					<dd>{{freeze}}</dd>
				</dl>
			</div>
		</div>
		<div class="usermain">
			<dl>
				<dt>可用余额</dt>
				<dd>{{money}}</dd>
			</dl>
			<button class="withdrawal" @click="switchTo('/withdrawal')">提现</button>
			<button class="recharge" @click="switchTo('/recharge')">充值</button>
		</div>
		<div class="user-ge">
			<ul>
				<li @click="switchTo('/data')">
					<img class="usergeico" src="../assets/userge1.png" />
					<span>用户资料</span>
					<img class="go" src="../assets/go.png" />
				</li>
				<!--<li @click="switchTo('/mestrategy1')">
					<img class="usergeico" src="../assets/userge2.png" />
					<span>委托中的策略</span>
					<img class="go" src="../assets/go.png" />
				</li>
				<li @click="switchTo('/mestrategy')">
					<img class="usergeico" src="../assets/userge2.png" />
					<span>已委托的策略</span>
					<img class="go" src="../assets/go.png" />
				</li>-->
				<li @click="switchTo('/money')">
					<img class="usergeico" src="../assets/userge3.png" />
					<span>资金明细</span>
					<img class="go" src="../assets/go.png" />
				</li>
				<li @click="switchTo('/capital')">
					<img class="usergeico" src="../assets/userge6.png" />
					<span>资金记录</span>
					<img class="go" src="../assets/go.png" />
				</li>
				<li @click="switchTo('/news')">
					<img class="usergeico" src="../assets/userge7.png" />
					<span>消息中心</span>
					<img class="go" src="../assets/go.png" />
				</li>
			</ul>
		</div>
		<div class="user-ge1">
			<ul>
				<li @click="switchTo('/about')">
					<img class="usergeico" src="../assets/userge5.png" />
					<span>关于我们</span>
					<img class="go" src="../assets/go.png" />
				</li>
			</ul>
		</div>
		<mi-footer></mi-footer>
	</div>
</template>

<script>
	import footer from './footer';
	import axios from 'axios';
	export default {
	components: {
      'mi-footer': footer
    },
  data() {
    return {
      vipphone:'',
      has_sum:'',
      yingkui:'',
      freeze:'',
      vip_head:'',
      money:'',
      vip_bank:''
    };
  },methods:{
    		switchTo(path){
      			this.$router.push(path)
   			},getData(){
    	var qs = require('qs');
   			let token =sessionStorage.getItem('token');
   			axios.post(this.$root.aurl+'api/user/vip_info',qs.stringify({'key':token}))
   		 .then((res)=>{
   		 	//console.log(res)
   		 	if(res.data.code == 999){
   				this.$router.push('/login');
   		 		//this.$store.dispatch('token');
   		 	}else{
   		 		//console.log(res)
   		 		this.vip_head=res.data.data.vip_head;
   		 		this.vipphone=res.data.data.vip_phone;
   		 		this.has_sum=res.data.data.gethas.has_sum;
   		 		this.yingkui=res.data.data.gethas.yingkui;
   		 		this.freeze=res.data.data.money.freeze;
   		 		this.money=res.data.data.money.money;
   		 		this.vip_bank=res.data.data.vip_bank;
   		 	} 
   		 	
   		 		
   		 }).catch(function (error) {
          		console.log(error)
        	})
    }
    },mounted(){
   		this.getData();	 
   }
}
</script>

<style>
	.usertop{background: url(../assets/user-ico1.png) no-repeat;width: 100%;height: 11.75rem;}
	.userhead{width: 4.35rem;height: 4.35rem;float: left;background-size: 100% 100%;margin-top: 1.6rem;margin-left: 1.3rem;}
	.userhead img{width: 100%;height: 100%;border-radius: 50%;}
	.usertop p{font-size: 1.4rem;color: #FFFFFF;float: left;margin-top: 3.35rem;margin-left: 1.55rem;}
	.usertop ul{float: left;}
	.usertop li{float: left;}
	.usertop li img{width: 1.85rem;height: 1.85rem;margin-top: 2.9rem;margin-left: 1.55rem;}
	.usershow dl{width: 30%;float: left;margin-top: 1.15rem;}
	.usershow dl:first-child{width: 40%;}
	.usershow dt{font-size: 1.3rem;color: #ffddd4;}
	.usershow dd{font-size: 1.5rem;color: #FFFFFF;}
	.usermain{width: 100%;height: 4.95rem;background: url(../assets/usermain.png) no-repeat;}
	.usermain dl{float: left;margin-left: 1.45rem;margin-top: 0.8rem;}
	.usermain dt{font-size: 1.3rem;color: #FFFFFF;}
	.usermain dd{font-size: 1.5rem;color: #FFFFFF;}
	.usermain button{float: right;width: 6.05rem;height: 2.2rem;border-radius: 0.55rem;border: 0;font-size: 1.1rem;color: #FFFFFF;margin-top: 1.15rem;}
	.recharge{background: #ffcd4b;margin-right: 1.15rem;}
	.withdrawal{background: #49a8fc;margin-right: 2.05rem;}
	.user-ge{width: 100%;overflow: hidden;}
	.user-ge li{width: 100%;height: 4.6rem;border-bottom: 1px solid #d6d6d6;}
	.user-ge .usergeico{float: left;width: 1.6rem;height: 1.55rem;margin-top: 1.25rem;margin-left: 1.6rem;}
	.user-ge li > span{font-size: 1.5rem;float: left;color: #404040;line-height: 4.2rem;margin-left: 0.45rem;}
	.user-ge .go{float: right;width: 0.55rem;height: 1.2rem;margin-top: 1.4rem;margin-right: 1.9rem;}
	.user-ge li p{float: right;line-height: 4.2rem;margin-right: 0.6rem;color: #c8c8c8;font-size: 1rem;}
	.user-ge li p span{color: #5b5b5b;}
	.user-ge li:last-child{border-bottom: 0;}
	.user-ge1{width: 100%;overflow: hidden;margin-top: 0.9rem;}
	.user-ge1 li{width: 100%;height: 4.6rem;}
	.user-ge1 .usergeico{float: left;width: 1.6rem;height: 1.4rem;margin-top: 0.8rem;margin-left: 1.6rem;}
	.user-ge1 li > span{font-size: 1.5rem;float: left;color: #404040;line-height: 3.2rem;margin-left: 0.45rem;}
	.user-ge1 .go{float: right;width: 0.55rem;height: 0.95rem;margin-top: 1.125rem;margin-right: 1.9rem;}
</style>


