<template>
	<div>
		<div class="header">
			<router-link to="/" class="headerleft">
			    <mt-button icon="back"></mt-button>
  			</router-link>
  			<ul>
  				<li class="select"  @click="switchTo('/quotation')">自选</li>
  				<li @click="switchTo('/optional')">行情</li>
  			</ul>
  			<router-link :to="{ name:'search1'}" class="headerright">
			    <mt-button icon="search" ></mt-button>
  			</router-link>
		</div>
		<div class="optional">
			
		<table cellpadding="0" cellspacing="0" border="0">
			<tr>
				<th>名称</th>
				<th>最新价</th>
				<th>幅度</th>
			</tr>
			<tr v-for="item in list">
				
				<td>
					<router-link :to="{ name:'buy', params: {id: item.gupiao_code} }">
					<dl>
						<dt>{{item.gupiao_name}}</dt>
						<dd>{{item.gupiao_code}}</dd>
					</dl>
					</router-link>
				</td>
				<td>
					<router-link :to="{ name:'buy', params: {id: item.gupiao_code} }">
						<span v-bind:style="{'color':(item.zdstatus == 1 ? '#ec3c08' :'#3da32c')}">{{item.price}}</span>
					</router-link>
				</td>
				<td>
					<router-link :to="{ name:'buy', params: {id: item.gupiao_code} }">
					<p v-bind:style="{'background':(item.zdstatus == 1 ? '#ec3c08' :'#3da32c')}">{{item.zdrate}}%</p>
					</router-link>
				</td>
				
			</tr>
			
		</table>
		</div>
		<mi-footer></mi-footer>
	</div>
</template>

<script>
	import footer from './footer';
	import axios from 'axios'
	export default {
	components: {
      'mi-footer': footer
    },
  data() {
    return {
      list:[],
      
    };
  },methods:{
    		switchTo(path){
      			this.$router.push(path)
   			},getData(){
                    //请求数据
                    var api=this.$root.aurl+'api/user/getgupiaorss_list';
                    
                    axios.get(api).then((res)=>{
   		 				//	console.log(res)
		   		 					if(res.data.code == 999){
		   				this.$router.push('/login');
		   		 	}else{
   		 					this.list= res.data.data
					}	
   		 				
   		 			})
                    
           }
   },mounted(){
  		this.getData();
  		
  }
}
</script>

<style>
	.header{width: 100%;height: 4.1rem;background: #ec3c08;overflow: hidden;display: flex;}
	.header .mint-button{background: none;color: #FFFFFF;height:4.1rem;border: 0;box-shadow: none;}
	.header ul{display: flex;height: 3.1rem;margin-top: 0.5rem;border: 0.05rem solid #FFFFFF;border-radius: 0.5rem;overflow: hidden;-webkit-box-align: center;font-size: 18px;-webkit-box-pack: center;justify-content: center;white-space: nowrap;flex: 1 1 0%;}
	.header li{float: left;width: 50%;line-height: 2.75rem;font-size: 1.2rem;color:#ffffff;}
	.header .select{background: #FFFFFF;color: #ec3c08;}
	.headerleft{display: flex;flex: 1 1 0%;}
	.headerright{display: flex;flex: 1 1 0%;justify-content: flex-end;}
	.optshow {width: 100%;overflow: hidden;}
	.optional table{width: 100%;}
	.optional th{background: #f2f6ff;height: 3rem;font-size: 1.4rem;}
	.optional td{border-bottom: 0.05rem solid #c7c5c5;}
	.optional td dl{font-size: 1.3rem;color: #252622;margin-top: 0.9rem;margin-bottom: 1.1rem;}
	.optional td span{display: block;font-size: 1.2rem;}
	.optional td p{width: 4.85rem;height: 2.05rem;color: #FFFFFF;font-size: 1.2rem;line-height: 2.05rem;margin: 0 auto;border-radius: 0.5rem;}
	.optional{width: 100%;overflow: hidden;margin-bottom: 6rem;}
	.optional table a{width: 100%;height: 100%;}
</style>


