<template>
	<div>
		<div class="header">
			<router-link to="/index" slot="left">
			    <mt-button icon="back"></mt-button>
  			</router-link>
  			<ul>
  				<li  @click="switchTo('/recharge')">线上充值</li>
  				<li class="select"  @click="switchTo('/recharge1')">线下充值</li>
  			</ul>
		</div>
		<div class="regs" >
			<div class="resli" v-for="(item,key) in list">
				<div class="reslimain regs1" :class="key==selected?'select':''" v-if="item.type == 0" @click="son(item,key)">
				<h3>{{item.title}}</h3>
				<div class="clear"></div>
				<ul>
					<li>收款账号：{{item.number}}</li>
					<li>收款人姓名：{{item.name}}</li>
					<li>所属银行：{{item.bankname}}  </li>
					<li>开户行：{{item.bank_address}}</li>
				</ul>
				</div>
				<div class="reslimain res2" :class="key==selected?'select':''" v-else @click="son(item,key)">
					<h3>{{item.title}}</h3>
				<p>支付宝名称：{{item.number}}</p>
				<p>扫描二维码转账</p>
				<img :src="$root.aurl+item.img_url"/>
				</div>
				<div class="clear"></div>
			</div>
			
			
			<div class="reg1ge">
				<p>充值金额</p>
				<input type="" v-model="input1"/>
			</div>
			<div class="reg1ge">
				<p>备注信息</p>
				<textarea v-model="textare"></textarea>
			</div>
			<button class="reg2btn" @click="reg2btn()">确认充值</button>
		</div>
	</div>
</template>

<script>
	import axios from 'axios';
	import $ from 'jquery'
	export default {
		data(){
			return{
				list:[],
				selected:-1,
				input1:'',
				textare:'',
				texid:''
			}
		},methods:{
			son(item,key){
        		this.selected=key;
        		var api=this.$root.aurl+'api/user/payofftype';
        		axios.get(api).then((res)=>{
        		this.texid = res.data.data[key].id;
        		})
   			},
			switchTo(path){
      			this.$router.push(path)
   		},getData(){
				var api=this.$root.aurl+'api/user/payofftype';
     		axios.get(api).then((res)=>{
     			//console.log(res)
        			this.list= res.data.data
        		})
		},reg2btn(){
			var input1 = this.input1;
			var textare = this.textare;
			var texid = this.texid;
			var qs = require('qs');
			var that = this
			axios.post(this.$root.aurl+"api/user/payoff",qs.stringify({'payofftype':texid,'payoffmoney':input1,'payoffinfo':textare})) 
    		.then(function(res){
    			//console.log(res)
    			if(res.data.code == '400'){
    				that.$toast.center(res.data.error);
    			}else{
    				that.$toast.center(res.data.data);
    			}
    			
    		})
		}
		},mounted(){
			$('.regsmain').on('click',function(){
				$('.regsmain').eq($(this).index()).addClass('select').siblings().removeClass('select');
			})
			this.getData()
		}
	}
</script>

<style>
	.header{width: 100%;height: 4.1rem;background: #ec3c08;overflow: hidden;}
	.header .mint-button{background: none;float: left;color: #FFFFFF;height:4.1rem;border: 0;box-shadow: none;}
	.header ul{width: 40.69%; height: 2.75rem;border: 0.05rem solid #FFFFFF;margin: 0.675rem auto;border-radius: 0.6rem;overflow: hidden;}
	.header li{float: left;width: 50%;line-height: 2.75rem;font-size: 1.2rem;color:#ffffff;}
	.header .select{background: #FFFFFF;color: #ec3c08;}
	.regs .select{border: solid 0.05rem #ec3c08;}
	.regs .select h3{color: #ec3c08;}
	.regs .select p{color: #ec3c08;}
	.regs .select li{color: #ec3c08;}
	.regs1{width: 95.41%;height: 7.65rem;border: solid 0.05rem #000000;margin: 1.1rem auto 0;}
	.regs1 h3{float: left;margin-left: 1.1rem;margin-top: 0.8rem;margin-bottom: 1rem;font-size: 1.4rem;}
	.regs1 li{text-align: left;padding-left: 0.5rem;float: left;line-height: 1.8rem;font-size: 1.2rem;}
	.regs1 li:nth-child(odd){width: calc(60% - 0.5rem);}
	.regs1 li:nth-child(even){width: calc(40% - 0.5rem);}
	.res2{width: calc(45.69% - 0.1rem);height: 18.85rem;border: solid 0.05rem #000000;float: left;margin: 0.6rem 2.155% 0 2.155%;}
	.res2 h3{font-size: 1.2rem;color: #141414;margin-top: 0.3rem;margin-bottom: 0.3rem;}
	.res2 p{line-height: 1.8rem;font-size: 1.2rem;color: #141414;}
	.reg1ge{width: 95.41%;margin: 1.1rem auto 0;overflow: hidden;}
	.reg1ge p{float: left;font-size: 1.2rem;line-height: 2.4rem;}
	.reg1ge input{float: left;width: 78%;margin-left: 3%;padding-left: 2%;outline: none;height: 2rem;font-size: 1.4rem;}
	.reg1ge textarea{float: left;width: 78%;margin-left: 3%;padding-left: 2%;outline: none;height: 6rem;font-size: 1.4rem;}
	.reg2btn{width: 60%;height: 3rem;font-size: 1.4rem;background: #ec3c08;border-radius: 0.3rem;margin: 2rem auto 4rem;border:none;outline: none;color: #FFFFFF;}
	.reslimain img{width: 80%;height: 12rem;}
</style>


