<template>
	<div>
		<mt-header title="资金明细 ">
		  <router-link to="/user" slot="left">
		    <mt-button icon="back"></mt-button>
		  </router-link>
		</mt-header>
		
		<div class="money">
			<ul class="moneybtn">
				<li @click="tabsSwitch()" class="select"><i></i>全部</li>
				<li @click="tabsSwitch1()"><i></i>近1个月</li>
				<li @click="tabsSwitch2()"><i></i>近3个月</li>
				<li @click="tabsSwitch3()"><i></i>近1年</li>
			</ul>
			<div class="clear"></div>
				<div class="moneytabletop">
					<ol>
						<li style="width: 20%;">交易时间</li>
						<li style="width: 20%;">交易类型</li>
						<li style="width: 35%;">交易明细</li>
						<li style="width: 25%;">交易金额</li>
					</ol>
				</div>
				<mi-all v-if='showall'></mi-all>
				<mi-week v-if='showweek'></mi-week>
				<mi-month v-if='showmonth'></mi-month>
				<mi-year v-if='showyear'></mi-year>
		</div>
		</scroller>
	</div>
</template>

<script>
	import $ from 'jquery'
	import all from './detailed/all';
	import week from './detailed/week';
	import month from './detailed/month';
	import year from './detailed/year';
	
	export default {
		components: {
			'mi-all': all,
			'mi-week': week,
			'mi-month': month,
			'mi-year': year,
		},
		data(){
			return{
				showall: true,
			  showweek: false,
			  showmonth: false,
			  showyear: false,
			}
		},methods:{
			tabsSwitch(){
   			this.showall = true;
   			this.showweek = this.showmonth = this.showyear = false;
   		},tabsSwitch1(){
   			this.showweek = true;
   			this.showall = this.showmonth = this.showyear = false;
   		},tabsSwitch2(){
   			this.showmonth = true;
   			this.showall = this.showweek = this.showyear = false;
   		},tabsSwitch3(){
   			this.showyear = true;
   			this.showall = this.showmonth = this.showweek = false;
   		}
		},mounted(){
			$(".money .moneybtn li").on('click',function(){
				$(".money .moneybtn li").eq($(this).index()).addClass('select').siblings().removeClass('select');
			})
			
		}
	}
</script>

<style>
	.money{width: 100%;overflow: hidden;}
	.moneybtn li{float: left;padding: 0.2rem 0.6rem;border-radius: 0.25rem;font-size: 1rem;color: #787878;margin-top: 0.6rem;margin-left: 1rem;}
	.money .select{background: #ec3e2e;color: #ffffff;}
	.moneytabletop{width: 100%;margin-top: 1.3rem;}
	.moneytabletop li{height: 3.5rem;line-height: 3.5rem;background: #f2f3f3;float: left;}
	.moneytablemain{width: 100%;overflow: hidden;}
	.moneytablemain li{height: 3rem;line-height: 3rem;border-bottom: 0.05rem solid #e2e1e1;float: left;overflow: hidden;text-overflow: ellipsis;white-space: nowrap;}
</style>


