<template>
	<div>
		<mt-header :title="title">
		  <router-link to="/press" slot="left">
		    <mt-button icon="back"></mt-button>
		  </router-link>
		</mt-header>
		<div class="newmain" v-html="content">
			{{content}}
		</div>
	</div>
</template>

<script>
	import axios from 'axios'
	export default {
    data(){
    	return{
    		title:'',
    		content:''
    	}
    },
        mounted(){
                var id=this.$route.params.id;
                //调用请求数据的方法

                this.requestData(id);

        },
    methods:{
    	requestData(id){
                var api=this.$root.aurl+'api/index/newdetail?id='+id;
                axios.get(api).then((res)=>{
                     console.log(res)
                        this.title=res.data.data.title;
                        this.content=res.data.data.content;
                })
            },
    switchTo(path){
      
      this.$router.push(path)
    }
   }
    }
</script>

<style>
	.newmain{width: 95%;margin: 0.5rem auto ;text-align: left;font-size: 1.2rem;text-indent: 2rem;line-height: 2rem;}
	.newmain p{margin-bottom: 1rem;}
</style>


