		 		var api=this.$root.aurl+'api/user/match_index';
		 		axios.post(api).then((res)=> {
		 			console.log(res)
		 			this.head_img = res.data.data.match_info.head_img
		 			this.match_money = res.data.data.match_info.match_money
		 			this.vip_id = res.data.data.match_info.vip_id
		 			this.vip_name = res.data.data.match_info.vip_name
		 			this.yest_porify= res.data.data.yest_porify
		 			this.ysypaihang=res.data.data.ysypaihang
		 			this.wsypaihang=res.data.data.wsypaihang
		 			this.rsypaihang=res.data.data.rsypaihang
		 			this.zsypaihang=res.data.data.zsypaihang
		 		})
		 	},switchTo(path){
      this.$router.push(path)
    }
		 },mounted(){
		 	this.getData();
		 }
