<template>
	<div>
		<mt-header title="资金记录">
		  <router-link to="/user" slot="left">
		    <mt-button icon="back"></mt-button>
		  </router-link>
		</mt-header>
		<div class="money">
			<ul>
				<li @click="tabsSwitch1()" class="select"><i></i>充值</li>
				<li @click="tabsSwitch2()"><i></i>提现</li>
			</ul>
			<div class="clear"></div>
				
				
				<mi-week v-if='showweek'></mi-week>
				<mi-month v-if='showmonth'></mi-month>
		</div>
	</div>
</template>

<script>
	import $ from 'jquery'
	import week from './capital/recharge';
	import month from './capital/withdrawal';
	
	export default {
		components: {
			
			'mi-week': week,
			'mi-month': month,
		},
		data(){
			return{
			  showweek: true,
			  showmonth: false,
			}
		},methods:{
			tabsSwitch1(){
   			this.showweek = true;
   			this.showall = this.showmonth = false;
   		},tabsSwitch2(){
   			this.showmonth = true;
   			this.showall = this.showweek = false;
   		}
		},mounted(){
			$(".money li").on('click',function(){
				$(".money li").eq($(this).index()).addClass('select').siblings().removeClass('select');
			})
			
		}
	}
</script>

<style>
	.money{width: 100%;overflow: hidden;}
	.money > ul li{float: left;padding: 0.2rem 0.6rem;border-radius: 0.25rem;font-size: 1rem;color: #787878;margin-top: 0.6rem;margin-left: 1rem;}
	.money .select{background: #ec3e2e;color: #ffffff;}
	.moneytabletop{width: 100%;margin-top: 1.3rem;}
	.moneytabletop li{height: 3.5rem;line-height: 3.5rem;background: #f2f3f3;float: left;}
	.moneytablemain{width: 100%;overflow: hidden;}
	.moneytablemain li{height: 3rem;line-height: 3rem;border-bottom: 0.05rem solid #e2e1e1;float: left;overflow: hidden;text-overflow: ellipsis;white-space: nowrap;}
</style>


