<template> 
		<div>
		    <canvas id="canvasKL" width="414" height="390" style="z-index: 2;">
		    	<p>你的浏览器不支持html5哟</p>
		    </canvas>
		    <div id="debug"></div>
		<p id="daychart" style="display: none;">{{mins}}</p>
		</div>
</template> 
<script>
	import $ from 'jquery';
	import axios from 'axios';
export default {
		data(){
			return{
				mins:[]
			}
		},methods:{
			getminsData(id){
   			var api=this.$root.aurl+'api/index/day_k?code='+id;
   			axios.get(api).then((res)=>{
   				//console.log(res)
   					  this.mins = res.data.data
   			})
   		}
		},mounted(){
			var id=this.$route.params.id
			this.getminsData(id)
		},updated(){
			$.getScript("http://qaz.sdruantao.com/kdata/k-data.js")
			$.getScript("http://qaz.sdruantao.com/kdata/rk.js")
			$.getScript("http://qaz.sdruantao.com/kdata/tip.js")
			
		}
	}
	
	
			
	
</script>
<style>
#canvasKL{
  margin: 0 auto;
  display: block;
}

</style>


