<template>
	<div>
		<mt-header title="用户信息">
		  <router-link to="/index" slot="left">
		    <mt-button icon="back"></mt-button>
		  </router-link>
		</mt-header>
		<div class="infortop">
			<img class="inforhead" :src="$root.aurl+head_img" />
			<dl>
				<dt>{{vip_name}}</dt>
				<dd>关注热度：<span>{{hotnum}}</span></dd>
			</dl>
			<ul>
				<li>最近发布时间：{{last_times}}</li>
				<li v-if="rss_status == 1" @click="subscribe(userid)"><button>已订阅</button></li>
		  		<li v-else><button @click="subscribe(userid)" class="subscribe">订阅</button></li>
			</ul>
		</div>
		<div class="competition">
			<div class="competition-title">
				<p>炒股大赛</p>
			</div>
			<div class="competition-main">
				<ul>
					<li>
						<img src="../assets/comp1.png" />
						<dl>
							<dt>总资产</dt>
							<dd>{{sum_shizhi}}元</dd>
						</dl>
						<div class="clear"></div>
						<p>月收益率{{profits_ratey}}%</p>
					</li>
					<li>
						<img src="../assets/comp2.png" />
						<dl>
							<dt>总收益率</dt>
							<dd>{{profits_rate}}%</dd>
						</dl>
						<div class="clear"></div>
						<p>周收益率{{profits_ratew}}%</p>
					</li>
					<li>
						<img src="../assets/comp3.png" />
						<dl>
							<dt>交易次数</dt>
							<dd>{{countid}}次</dd>
						</dl>
						<div class="clear"></div>
						<p>日收益率{{profits_rater}}%</p>
					</li>
					
				</ul>
			</div>
		</div>
		<div class="competition">
			<div class="competition-title">
				<p>实盘策略</p>
			</div>
			<div class="competition-main">
				<ul>
					<li>
						<img src="../assets/comp1.png" />
						<dl>
							<dt>持仓市值</dt>
							<dd>{{sum_shizhi1}}元</dd>
						</dl>
						<div class="clear"></div>
						<p>月收益率{{shipany}}%</p>
					</li>
					<li>
						<img src="../assets/comp2.png" />
						<dl>
							<dt>总收益率</dt>
							<dd>{{profits_rate1}}%</dd>
						</dl>
						<div class="clear"></div>
						<p>周收益率{{shipanw}}%</p>
					</li>
					<li>
						<img src="../assets/comp3.png" />
						<dl>
							<dt>交易次数</dt>
							<dd>{{countid1}}次</dd>
						</dl>
						<div class="clear"></div>
						<p>日收益率{{shipanr}}%</p>
					</li>
					
				</ul>
			</div>
		</div>
		<div class="infordata" v-for="item in list">
			<div class="infordata-top">
			<dl>
				<dt>股票数：<span>{{item.deal_number}}</span></dt>
				<dd v-if="item.cztype == 2">收益率：<span class="red">{{item.profits_rate}}%</span></dd>
				<dd v-else></dd>
			</dl>
			<dl>
				<dt v-if="item.cztype == 2">卖出价：<span>{{item.sell_price}}</span></dt>
				<dt v-else>买入价：<span>{{item.trush_price}}</span></dt>
				<dd>当前价：<span class="red">{{item.now_price}}</span></dd>
			</dl>
			<dl>
				<dt>止盈价：<span>{{item.stop_win}}</span></dt>
				<dd>止损价：<span class="green">{{item.stop_down}}</span></dd>
			</dl>
			</div>
			<div class="infordata-main">
				<img :src="$root.aurl+item.head_img"/>
				<dl>
					<dt>{{item.vip_name}}</dt>
					<dd v-if="item.cztype == 2">{{item.sell_times}}</dd>
					<dd v-else>{{item.creat_times}}</dd>
				</dl>
				<dl>
					<dt>{{item.gupiao_name}}</dt>
					<dd>{{item.gupiao_code}}</dd>
				</dl>
				<router-link class="sc-data" :to="{ name:'buy', params: {id: item.gupiao_code} }" >跟买</router-link>
			</div>
		</div>
	</div>
</template>

<script>
	import axios from 'axios';
	export default {
		data() {
			return{
				sum_shizhi:'',
      			profits_rate:'',
      			countid:'',
				sum_shizhi1:'',
      			profits_rate1:'',
      			countid1:'',
      			profits_ratey:'',
      			profits_ratew:'',
      			profits_rater:'',
      			shipany:'',
      			shipanw:'',
      			shipanr:'',
      			vip_name:'',
      			head_img:'',
      			hotnum:'',
      			rss_status:'',
      			last_times:'',
      			userid:'',
      			list:[],
			}	
			},methods:{
				getData(id){
					var api=this.$root.aurl+'api/user/userhome?id='+id;
					
					axios.post(api).then((res)=> {
						console.log(res)
						this.sum_shizhi=res.data.data.dasai.sum_shizhi;
						this.profits_rate=res.data.data.dasai.profits_rate;
						this.countid=res.data.data.dasai.countid;
						this.sum_shizhi1=res.data.data.shipan.sum_shizhi;
						this.profits_rate1=res.data.data.shipan.profits_rate;
						this.countid1=res.data.data.shipan.countid;
						this.profits_ratey=res.data.data.dasaiy.profits_rate;
						this.profits_ratew=res.data.data.dasaiw.profits_rate;
						this.profits_rater=res.data.data.dasair.profits_rate;
						this.shipany=res.data.data.shipany.profits_rate;
						this.shipanw=res.data.data.shipanw.profits_rate;
						this.shipanr=res.data.data.shipanr.profits_rate;
						this.vip_name=res.data.data.info.vip_name;
						this.head_img=res.data.data.info.head_img;
						this.hotnum=res.data.data.info.hotnum;
						this.rss_status=res.data.data.info.rss_status;
						this.last_times=res.data.data.info.last_times;
						this.userid=res.data.data.info.id;
						this.list=res.data.data.list;
						
					})
				},subscribe(e){
					var qs = require('qs');
					console.log(e)
					var api=this.$root.aurl+'api/user/setRss';
					axios.post(api,qs.stringify({'id':e})).then((res)=> {
						//console.log(res)
						this.getData();
					})
				},switchTo(path){
      				this.$router.push(path)
    			}
				
			},mounted(){
				var id=this.$route.params.id;
                //调用请求数据的方法

                this.getData(id);
				
			}
		
	}
</script>

<style>
	.infortop{width: 100%;height: 8.5rem;overflow: hidden;}
	.inforhead{float: left;width: 5.5rem;height: 5.5rem;border-radius: 50%;margin-left: 0.65rem;margin-top: 1.2rem;}
	.infortop dl{float: left;margin-top: 2.55rem;margin-left: 2rem;text-align: left;line-height: 1.8rem;}
	.infortop dt{font-size: 1.3rem;color: #000000;}
	.infortop dd{font-size: 1.1rem;color: #656464;}
	.infortop dd span{color: #da2525;font-weight: 600;}
	.infortop ul{margin-top: 2.05rem;float: right;margin-right: 1rem;}
	.infortop button{width: 5.9rem;height: 2.2rem;background: #ec9e9e;border-radius: 0.35rem;color: #FFFFFF;border: 0;outline: none;margin-top: 0.7rem;}
	.infortop .subscribe{background: #ec4229;}
	.competition{width: 100%;overflow: hidden;}
	.competition-title{width: 100%;height: 2.75rem;border-bottom: 0.05rem solid #c9c9c9;}
	.competition-title p{width: 6.15rem;border-bottom: 0.15rem solid #ec3e2e;font-size: 1.2rem;color: #3e3e3e;height: 2.7rem;line-height:2.7rem;margin-left: 0.65rem;}
	.competition-main{width: 100%;overflow: hidden;height: 8rem;}
	.competition-main li{width: calc(33.33% - 0.05rem);float: left;border-right: 0.05rem solid #c9c9c9;margin-top: 1.2rem;}
	.competition-main li:last-child{border: 0;}
	.competition-main li img{float: left;width: 2.65rem;height: 3.1rem;margin-left: 1rem;margin-top: 0.95rem;}
	.competition-main li dl{float: left;margin-left: 1rem;margin-top: 1.45rem;text-align: left;}
	.competition-main li dt{font-size: 1.2rem;color: #929292;}
	.competition-main li dd{font-size: 1.2rem;color: #ec3e2e;}
	.competition-main li p{font-size: 1.2rem;color: #929292;margin-top: 1.2rem;text-align: center;}
	.infordata{width: 100%;border-bottom: 0.1rem solid #c8c8c8;overflow: hidden;margin-top: 1rem;padding-bottom: 1.2rem;}
	.infordata-top{width: 100%;overflow: hidden;}
	.infordata-top dl{float: left;width: 33.33%;font-size: 1.2rem;color: #adaeae;line-height: 1.8rem;}
	.infordata-top dl span{color: #3d3d3d;}
	.infordata-top dl .red{color: #f35358;}
	.infordata-top dl .green{color: #01af1a;}
	.infordata-main img{width: 3.8rem;height: 3.8rem;float: left;margin-left: 1.95rem;margin-top: 1rem;border-radius: 50%;}
	.infordata-main dl{float: left;margin-left: 2.1rem;margin-top: 1.3rem;line-height: 1.8rem;}
	.infordata-main dt{font-size: 1.4rem;color: #5d5d5e;}
	.infordata-main dd{font-size: 1.2rem;color: #adaeae;}
	.infordata-main a{float: left;width: 4.85rem;height: 2.45rem;line-height: 2.45rem;text-decoration: none;border: solid 0.05rem #ed4e53;font-size: 1.4rem;color: #e91c23;border-radius: 0.55rem;margin-left: 1rem;margin-top: 1.7rem;}
</style>


