<template>
	<div>
		<div class="header">
			<router-link to="/" slot="left">
			    <mt-button icon="back" class="headerleft"></mt-button>
  			</router-link>
  			<input type="text" class="searchinput" @keyup='search($event)'/>
		</div>
		<div class="searchmain">
				<ul class="searchfirst">
					<li>名称</li>
					<li>代码</li>
					<li>简拼</li>
				</ul>
				<ul v-for="item in list" >
					<router-link class="sc-data" :to="{ name:'stock',path:'/stock', params: {id: item.code} }" >
					<li>{{item.title}}</li>
					<li>{{item.code}}</li>
					<li>{{item.pinyin}}</li>
					</router-link>
				</ul>
		</div>
	</div>
</template>

<script>
	import axios from 'axios';
	export default {
		data(){
			return{
				list:[]
			}
		},methods:{
            search:function(e){
                    //请求数据
                    var api=this.$root.aurl+'api/index/invest_search';
                    let searchvalue = e.currentTarget.value;
                    axios.get(api,{
                    params: { code: searchvalue}
               }).then(function(res){
               //	console.log(res)
               	if(res.data.length!=0){
                       this.list = res.data.data;
               	}
                    }.bind(this))
            }
            
	},mounted(){
            
      }
}
</script>

<style>
	.header{width: 100%;height: 4.1rem;background: #ec3c08;overflow: hidden;position: relative;}
	.header .mint-button{background: none;color: #FFFFFF;height:4.1rem;border: 0;box-shadow: none;}
	.searchinput{width: 69.02%;position: absolute;left: 15.49%;height: 2.75rem;background: rgba(248,248,248,0.58);border: 0;margin-top: 0.675rem;border-radius: 0.95rem;outline: none;text-align: center;color: #FFFFFF;}
	.searchmain{width: 100%;overflow: hidden;}
	.searchmain li{width: 33.33%;float: left;height: 2.9rem;line-height: 2.9rem;font-size: 1.2rem;color: #787878;border-bottom: 0.05rem solid #eee;}
	.searchfirst{background: rgba(198,198,198,0.26);width: 100%;height: 2.9rem;}
	.searchmain a{width: 100%;text-decoration: none;}
</style>


