<template>
	<div>
		<mt-header title="新闻资讯 ">
		  <router-link to="/user" slot="left">
		    <mt-button icon="back"></mt-button>
		  </router-link>
		</mt-header>
		<div>
			<ul class="presswrap">
				<li @click="tabsSwitch1()" class="select"><i></i>行情资讯</li>
				<li @click="tabsSwitch2()"><i></i>网站公告</li>
			</ul>
			<div class="clear"></div>
				<mi-week v-if='showweek'></mi-week>
				<mi-month v-if='showmonth'></mi-month>
		</div>
		
		<mi-footer></mi-footer>
	</div>
</template>

<script>
	import axios from 'axios'
	import footer from './footer';
	import week from './press/hqlist';
	import month from './press/gglist';
	import $ from 'jquery'
	export default {
		components: {
      'mi-footer': footer,
		'mi-week': week,
		'mi-month': month,
    },
		data(){
			return{
				selected: '1',
      			valuezd:false,
      			hqlist:[],
      			gglist:[],
      			showweek: true,
      			showmonth: false,
			}
		},methods: {
			tabsSwitch1(){
   			this.showweek = true;
   			this.showall = this.showmonth = false;
   		},tabsSwitch2(){
   			this.showmonth = true;
   			this.showall = this.showweek = false;
   		}
          },mounted(){
			$(".presswrap li").on('click',function(){
				$(".presswrap li").eq($(this).index()).addClass('select').siblings().removeClass('select');
			})
		}
	}
</script>

<style>
	.mint-navbar .mint-tab-item{font-size: 1.2rem;color: #838383;}
	.mint-navbar .mint-tab-item.is-selected{border-bottom: 0.15rem solid rgba(236,63,37,0.99);color: #000000;margin-bottom: -0.075rem;}
	.presswrap{width: 100%;}
	.presswrap li{width: 50%;float: left;line-height: 3rem;font-size: 1.4rem;border-bottom: 0.05rem solid #999;}
	.presswrap .select{border-bottom: 0.15rem solid #ec3c08;color: #000000;}
</style>


