<template>
	<div>
		<mt-header title="消息中心 ">
		  <router-link to="/user" slot="left">
		    <mt-button icon="back"></mt-button>
		  </router-link>
		</mt-header>
		<scroller style="margin-top: 5rem;margin-bottom: 0rem;" :on-refresh="refresh" :on-infinite="infinite">
		<div class="news">
			
			<ul>
				<li @click="ShouPup(index)" v-for="(item,index) in list">
					<p>{{item.content}}</p>
					<span>{{item.send_times}}</span>
				</li>
			</ul>
			
		</div>
		</scroller>
		<mt-popup v-model="popupVisible1" popup-transition="popup-fade" class="mint-popup-1" :style="{ top: buttonBottom + 30 + '%' }">
			<p>{{content}}</p> 
		</mt-popup>
      	
	</div>
</template>

<script>
	import axios from 'axios';
	 export default {
    data() {
      return {
      	popupVisible1: false,
      	buttonBottom: 0,
      	list:[],
      	content:'',
      	offset : 0,
      }
     },methods: {
      		ShouPup(e) {
                this.popupVisible1 = true;
                var api=this.$root.aurl+'api/user/innermsglist';
        	axios.get(api).then((res)=>{
        			this.content=res.data.data.data[e].content;
        			
        		})
           },getData(){
           	var api=this.$root.aurl+'api/user/innermsglist';
        	axios.get(api).then((res)=>{
        		//console.log(res)
        			this.list=res.data.data
        			
        		})},getData1(offset,done){
				var api=this.$root.aurl+'api/user/innermsglist';
     		axios.get(api,{params:{'page':offset}}).then((res)=>{
        			if(res.data.data == null ||res.data.data == ''){
        				this.searchvaldata = this.searchvaldata;
        				done(true)
        			}else{
        			if(res.data.data.length < 10 ){
        				this.offset = 0
        				done(true)
        				
        			}else{
        				if(done){
        				done()
        				}
        				}
        			if(offset === 1){
        				this.list = res.data.data
        			}else{
        				this.list = this.list.concat(res.data.data);
        			}	}
        		})
			},infinite (done) {
    			this.offset++    //每当向上滑动的时候就让页数加1
    			this.getData1(this.offset,done)
  			},
  				refresh (done) {
        		//这是向下滑动的时候请求最新的数据 
        		this.getData1(done) 
        		done(true)
        	}
           },mounted(){
			this.getData()
		}
    	}
</script>

<style>
	.mint-popup-1 {
        width: 200px;
        border-radius: 8px;
        padding: 10px;
        transform: translate(-50%, 0);

        h1 {
          font-size: 20px;
          color: #26a2ff;
        }

        p {
          margin-bottom: 10px;
        }
      }

      .mint-popup-1::before {
        triangle: 10px top #fff;
        content: '';
        position: absolute;
        top: -20px;
        right: 50px;
      }
      .mint-popup p{text-align: left;font-size: 1.2rem;}
	.news{width: 100%;overflow: hidden;}
	.news li{border-bottom: 0.05rem solid #cccccc;width: 100%;height: 2.6rem;line-height: 2.6rem;}
	.news p{float: left;width: 68%;overflow: hidden;text-overflow: ellipsis;white-space: nowrap;font-size: 1.2rem;padding-left: 2%;text-align: left;}
	.news span{float: right;width: 30%;font-size: 1.2rem;}
</style>


