<template>
	<div>
		<mt-header title="用户资料">
		  <router-link to="/user" slot="left">
		    <mt-button icon="back"></mt-button>
		  </router-link>
		</mt-header>
		<div class="data-ge">
			<ul>
				<li @click="switchTo('/data')">
					
					<span>手机号码</span>
					<img class="go" src="../assets/go.png" />
					<p>{{vip_phone}}</p>
				</li>
				<li @click="switchTo('/real')">
					
					<span>实名认证</span>
					<img class="go" src="../assets/go.png" />
					<p>{{vip_idcard}}</p>
				</li>
				<li @click="switchTo('/bank')">
					
					<span>提现银行卡</span>
					<img class="go" src="../assets/go.png" />
					<p v-if="vip_bank ==''">未设置</p>
					<p v-else="">已设置</p>
				</li>
			</ul>
		</div>
		<div class="data-ge1">
			<ul>
				<li @click="switchTo('/password')">
					<span>登录密码</span>
					<img class="go" src="../assets/go.png" />
					<p>修改</p>
				</li>
				<li @click="switchTo('/payment')">
					<span>支付密码</span>
					<img class="go" src="../assets/go.png" />
					<p>修改</p>
				</li>
			</ul>
		</div>
		<button class="signout" @click="logout()">退出登录</button>
		<mi-footer></mi-footer>
	</div>
</template>

<script>
	import footer from './footer';
	import axios from 'axios';
	export default {
  		name: 'page-navbar',
	components: {
      'mi-footer': footer
    },
  data() {
    return {
      vip_phone:'',
      vip_idcard:'',
      vip_bank:''
    };
  },methods:{
    		switchTo(path){
      			this.$router.push(path)
   			},logout(){
    	this.$store.commit('LOGOUT');
    	if (!this.$store.state.token) {
    		this.$toast.center('登出成功');
    		this.$router.push('/index')
    	}else{
    		this.$toast.center('登出失败');
    	}
    },getData(){
    	var qs = require('qs');
    	let token =sessionStorage.getItem('token');
   		axios.post(this.$root.aurl+'api/user/vip_data',qs.stringify({'key':token}))
   		 .then((res)=>{
   		 	
   		 	if(res.data.code == 999){
   		 		this.$router.push('/login');
   		 		this.$store.dispatch('token');
   		 	}else{
   		 	this.vip_phone = res.data.data.vip_phone;
   		 	this.vip_idcard = res.data.data.vip_idcard;
   		 	this.vip_bank = res.data.data.vip_bank;
   		 	}
   		 }).catch(function (error) {
          		console.log(error)
        	})
    }
    },mounted(){
   		this.getData();
   		 
   }
}
</script>

<style>
	.data-ge{width: 100%;overflow: hidden;}
	.data-ge li{width: 100%;height: 4.2rem;border-bottom: 1px solid #d6d6d6;}
	.data-ge li > span{font-size: 1.3rem;float: left;color: #404040;line-height: 4.2rem;margin-left: 1.95rem;}
	.data-ge .go{float: right;width: 0.55rem;height: 1rem;margin-top: 1.6rem;margin-right: 1.9rem;}
	.data-ge li p{float: right;line-height: 4.2rem;margin-right: 0.6rem;color: #c8c8c8;font-size: 1.2rem;}
	.data-ge li:last-child{border-bottom: 0;}
	.data-ge1{width: 100%;overflow: hidden;margin-top: 0.9rem;}
	.data-ge1 li{width: 100%;height: 4.2rem;}
	.data-ge1 li > span{font-size: 1.3rem;float: left;color: #404040;line-height: 4.2rem;margin-left: 1.95rem;}
	.data-ge1 .go{float: right;width: 0.55rem;height: 1rem;margin-top: 1.6rem;margin-right: 1.9rem;}
	.data-ge1 li p{float: right;line-height: 4.2rem;margin-right: 0.6rem;color: #c8c8c8;font-size: 1.2rem;}
	.signout{width: 85%;height: 3.35rem;line-height: 3.35rem;margin: 2.35rem auto 0;background: #ec3c08;border: 0;outline: none;border-radius: 0.65rem;font-size: 1.3rem;color: #FFFFFF;}
</style>


